import React from 'react';
import { formatValue } from '../../utils/format-value';
import styles from './MetricsSection.module.css';

const MetricsSection = (props) => {
  const { type = 'instagram', data, postCount, totalCalculatedPosts } = props;

  const renderIcon = (theType) => {
    if (theType === 'like') {
      return <img src='/images/icon-likes.png' alt='Like' />;
    } else if (theType === 'comment') {
      return <img src='/images/icon-comments.png' alt='Comments' />;
    } else if (theType === 'view') {
      return <img src='/images/icon-views.png' alt='views' />;
    } else if (theType === 'engagement') {
      return <img src='/images/icon-engagement.png' alt='Engagement' />;
    } else if (theType === 'confidence') {
      return <img src='/images/icon-confidence.png' alt='Confidence' />;
    } else if (theType === 'post') {
      return <img src='/images/icon-post.png' alt='Post' />;
    }
  };

  const renderMetric = (theVal, theText, theIcon, isPercentValue) => {
    let formattedValue;

    if (isPercentValue) {
      formattedValue = `${theVal?.toFixed(2)}%`;
    } else {
      formattedValue = formatValue(theVal);
    }
    return (
      <div className={styles['block-cx']}>
        <div className={styles['metrics-item-top']}>
          {theIcon ? renderIcon(theIcon) : ''}
          <p>{formattedValue}</p>
        </div>
        <p>{theText}</p>
      </div>
    );
  };

  const formatConfidenceField = (value) => {
    const formattedValue = 100 - value;

    if (Math.sign(formattedValue) === 1) {
      return `${formattedValue.toFixed(2)}%`;
    } else {
      return 'Low';
    }
  };

  const renderFetchMsg = (type, postsLength) => {
    switch (type) {
      case 'youtube-vid':
        return 'Data fetched for latest 20 videos';
      case 'youtube-shorts':
        return 'Data fetched for latest 20 shorts';
      case 'instagram':
        return 'Data fetched for latest 24 reels';
      case 'tiktok':
        return `Data fetched for latest ${postsLength} videos`;
      default:
        break;
    }
  };

  const renderTitle = (type) => {
    switch (type) {
      case 'youtube-vid':
        return 'Metrics (Videos)';
      case 'youtube-shorts':
        return 'Metrics (Shorts)';
      default:
        return 'Metrics';
    }
  };

  return (
    <div className={styles['metrics-box']}>
      <div className={styles['metrics-header']}>
        <h3>{renderTitle(type)}</h3>
        <p className={styles['small-msg']}>
          {renderFetchMsg(type, totalCalculatedPosts)}
        </p>
      </div>

      <div className={styles['metrics-cx']}>
        {renderMetric(data.metrics.medianLikes, 'Predicted Likes', 'like')}
        {renderMetric(
          data.metrics.medianComments,
          'Predicted Comments',
          'comment'
        )}
        {renderMetric(
          data.metrics.medianEngagement,
          'Predicted Engagement',
          'engagement'
        )}
        {renderMetric(data.metrics.averageViews, 'Average Views', 'view')}
        {renderMetric(data.metrics.medianViews, 'Predicted Views', 'view')}
        {renderMetric(data.metrics.maxViews, 'Max Views', 'view')}
        {renderMetric(data.metrics.minViews, 'Min Views', 'view')}
        {renderMetric(
          data.metrics.likesToViewsRatio.value,
          'Predicted Likes to Views Ratio',
          'like',
          true
        )}
        {renderMetric(
          data.metrics.commentsToLikesRatio.value,
          'Predicted Comments to Likes Ratio',
          'comment',
          true
        )}

        {/* Conditional rendering by using type because the other platform is not yet ready, while all of the object keys are available on the response */}
        {type === 'youtube-vid' || type === 'youtube-shorts' ? (
          <>
            {renderMetric(
              data.metrics.eRByViewsWithoutOutliers.value,
              'Predicted ER by Views',
              'engagement',
              true
            )}
            {renderMetric(
              data.metrics.eRByViewsWithOutliers.value,
              'Predicted ER by Views without outliers',
              'engagement',
              true
            )}
            {renderMetric(
              data.metrics.eRByFollowers.value,
              'Predicted ER by Followers',
              'engagement',
              true
            )}
            {renderMetric(
              data.metrics.eRCombined.value,
              'Predicted ER Combined (Videos + Shorts)',
              'engagement',
              true
            )}
          </>
        ) : (
          <>
            {renderMetric(
              data.metrics.engagementRate.value,
              'Predicted Engagement Rate',
              'engagement',
              true
            )}
          </>
        )}
        {renderMetric(
          formatConfidenceField(data.metrics.confidence),
          'Confidence',
          'confidence'
        )}

        {renderMetric(data.metrics.numberOfPeaks, 'Number of Peaks')}

        {renderMetric(data.metrics.peakAverage, 'Peak Average')}

        {type === 'x' || type === 'facebook' || type === 'linkedin'
          ? renderMetric(postCount, 'Total Post', 'post')
          : ''}
      </div>
    </div>
  );
};

export default MetricsSection;
