import React, { useContext, useEffect, useState } from 'react';

import ErrorAlert from '../../../../components/ErrorAlert';
import apiService from '../../../../utils/service';
import styles from '../OptionsPage/OptionsPage.module.css';
import { ToasterContext } from '../../../../Context/toaster';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import { LoadingContext } from '../../../../Context/loading';
import RefreshIcon from '@mui/icons-material/Refresh';
import AddBulkInfluencer from '../../../../components/AddBulkInfluencer';

const SuggestedUsersPage = (props) => {
  const { currCookie, userId, isAllowedToRequest } = props;

  const [status, setStatus] = useState('');
  const [errorMsg, setErrorMsg] = useState('');
  const [username, setUsername] = useState('');
  const [suggestedUsers, setSuggestedUsers] = useState([]);

  const { loading, setLoading } = useContext(LoadingContext);
  const { showToaster } = useContext(ToasterContext);

  const getXSuggestedUsersData = (refresh = false) => {
    // eslint-disable-next-line no-undef
    chrome.tabs.query({ active: true, currentWindow: true }, (tabs) => {
      // eslint-disable-next-line no-undef
      chrome.tabs.sendMessage(
        tabs[0].id,
        { action: 'getXSuggestedUsersData' },
        (response) => {
          // console.log(response);
        }
      );
    });
    setLoading(true)
    setSuggestedUsers([])
    setUsername('')
    setTimeout(() => {
      // eslint-disable-next-line no-undef
      chrome.storage.local.get('XSuggestedUsersData', (result) => {
        if (!result.XSuggestedUsersData.isElementAvailable) {
          setErrorMsg(
            "Can't find required information. Please make sure you're not following this profile."
          );
          setLoading(false)
        } else {
          setUsername(result.XSuggestedUsersData.username);
          setSuggestedUsers(result.XSuggestedUsersData.users);
          
          postSuggestedInfluencer(
            result.XSuggestedUsersData.username,
            result.XSuggestedUsersData.users
          );
        }
      });
    }, 3000);
  };
  const postSuggestedInfluencer = async (theUsername, suggestedUsers) => {
    setLoading(true);
    const postData = {
      username: theUsername,
      platform: 'twitter',
      users: suggestedUsers
    };

    try {
      const response = await apiService.post(
        `${process.env.REACT_APP_API_URL}/scraper/v1/suggested-influencer`,
        postData,
        {
          headers: {
            Authorization: `Bearer ${currCookie}`
          }
        }
      );

      if (response.data) {
        showToaster({
          severity: 'success',
          show: true,
          message: response.data.data
        });
  
      }
    } catch (error) {
      const errorMessage = error.response.data?.errors[0].detail || 'Failed to add suggested influencer data'

      showToaster({
        severity: 'error',
        show: true,
        message: errorMessage
      });
    }

    setLoading(false);
    setStatus('refresh');
  };
  const copySuggestedUsers = () => {
    const headerRow = ['Name', 'Username', 'Profile URL'];

    const dataRows = suggestedUsers.map((user) => [
      user.name,
      user.username,
      `https://x.com/${user.username}`
    ]);

    const allRows = [headerRow, ...dataRows];

    const formattedData = allRows.map((row) => row.join('\t')).join('\n');

    navigator.clipboard
      .writeText(formattedData)
      .then(() => {
        showToaster({
          severity: 'success',
          show: true,
          message: 'Data copied to clipboard'
        });
      })
      .catch((error) => {
        showToaster({
          severity: 'error',
          show: true,
          message: 'Failed to copy data to clipboard'
        });
      });
  };

  useEffect(() => {
    if (userId && isAllowedToRequest) {
      if (loading) {
        apiService.cancelAllRequests();
        setLoading(false);
      }

      getXSuggestedUsersData();
    }
  }, [userId, isAllowedToRequest]);

  return (
    <>
      {errorMsg ? (
        <ErrorAlert errorMsg={errorMsg} />
      ) : (
        <div className={styles['main-cont']}>
          <section className={styles['top-card']}>
            <div className={styles['left-box']}>
              <>
                <div className={styles['url-cx']}>
                  {username && (
                    <>
                      <h3>Suggested Users from:</h3>
                      <p>{username}</p>
                    </>
                  )}
                </div>
              </>
            </div>
            <div className={styles['right-box']}>
              <div className={styles['button-box']}>
                {status === 'refresh' && (
                  <>
                    <div
                      className={styles['refresh-cta']}
                      onClick={() => getXSuggestedUsersData()}
                    >
                      <RefreshIcon
                        style={{ color: '#500b75', height: '20px' }}
                      />
                      <span>Refresh</span>
                    </div>
                  </>
                )}
                {suggestedUsers && suggestedUsers.length ? (
                  <button onClick={copySuggestedUsers}>
                    <span>Copy Suggested Users Data</span>
                    <ContentCopyIcon
                      style={{ color: '#ffffff', height: '16px' }}
                    />
                  </button>
                ) : (
                  ''
                )}
              </div>
            </div>
          </section>
          {suggestedUsers && suggestedUsers.length ? (
            <section className={styles['last-card']}>
              <div>
                <AddBulkInfluencer
                  suggestedUsers={suggestedUsers}
                  currCookie={currCookie}
                  type="x"
                />
              </div>
            </section>
          ) : (
            ''
          )}
        </div>
      )}
    </>
  );
};

export default SuggestedUsersPage;
