import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";

import OptionsPage from "./components/OptionsPage";
import InvalidUrl from "../../modules/InvalidUrl";

const InstagramPage = (props) => {
  const { currentUrl = "", currCookie = null, isAllowedToRequest } = props;

  const [csrfToken, setCsrfToken] = useState(null);
  const [username, setUsername] = useState(null);
  const [isUrlInvalid, toggleInvalidUrl] = useState(null);
  const location = useLocation();
  const queryParams = new URLSearchParams(location.search);
  const queryType = queryParams.get("type");
  const queryUsername = queryParams.get("username");

  const extractUsernameFromUrl = () => {
    const urlParts = currentUrl.split("/");
    if (
      urlParts.length >= 4 &&
      urlParts[0] === "https:" &&
      urlParts[2] === "www.instagram.com" &&
      urlParts[3]
    ) {
      let possibleUsername = urlParts[3]
      let actualUsername = ''
      if (!possibleUsername.includes('?')) {
        actualUsername = possibleUsername
      } else {
        actualUsername = possibleUsername.split('?')[0]
      }
      setUsername(actualUsername)
    }
  }

  const checkInstaUrlValidity = (url) => {
    // Parse the URL
    let parsedUrl;
    try {
      parsedUrl = new URL(url);
    } catch (error) {
      return false; // Invalid URL format
    }

    // Check if it's an Instagram URL
    if (parsedUrl.hostname !== 'www.instagram.com' && parsedUrl.hostname !== 'instagram.com') {
      return false;
    }

    // Get the path without leading/trailing slashes
    const path = parsedUrl.pathname.replace(/^\/|\/$/g, '');

    // Index route
    if (path === '') {
      return false;
    }

    // Split the path into segments
    const segments = path.split('/');

    // Check for post or reel URLs
    if (segments.length > 1 && (segments[0] === 'p' || segments[0] === 'reel')) {
      return false;
    }

    // Check for explore page
    if (segments[0] === 'explore') {
      return false;
    }

    return true;
  };

  useEffect(() => {
    // Get token only if the state is empty
    if (!csrfToken) {
      // eslint-disable-next-line no-undef
      chrome.cookies.get(
        { url: "https://www.instagram.com", name: "csrftoken" },
        (cookie) => {
          setCsrfToken(cookie ? cookie.value : null);
        }
      );
    }

    if (checkInstaUrlValidity(currentUrl)) {
      toggleInvalidUrl(false)
      extractUsernameFromUrl()
    } else if (queryType && queryUsername && queryType === "instagram") {
      toggleInvalidUrl(false)
      setUsername(queryUsername);
    } else { 
      toggleInvalidUrl(true);
    }
  }, [currentUrl]);

  return (
    <>
      {isUrlInvalid ? (
        <InvalidUrl />
      ) : (
        <OptionsPage
          currCookie={currCookie}
          csrfToken={csrfToken}
          username={username}
          type="instagram"
          isAllowedToRequest={isAllowedToRequest}
        />
      )}
    </>
  );
};

export default InstagramPage;
