import axios from 'axios';
import { resetToken } from '../Context/auth';

const controllers = new Map();
const axiosInstance = axios.create();

axiosInstance.interceptors.request.use(
  config => {
    const controller = new AbortController();
    config.signal = controller.signal;
    controllers.set(config.url, controller);
    return config;
  },
  error => Promise.reject(error)
);

axiosInstance.interceptors.response.use(
  response => {
    controllers.delete(response.config.url);
    return response;
  },
  error => {
    if (error.status === 401) {
      resetToken()
    }
    if (error.config) {
      controllers.delete(error.config.url);
    }
    return Promise.reject(error);
  }
);

const get = (url, config = {}) => {
  return axiosInstance.get(url, config);
};

const post = (url, data, config = {}) => {
  return axiosInstance.post(url, data, config);
};

const cancelRequest = (url) => {
  const controller = controllers.get(url);
  if (controller) {
    controller.abort();
    controllers.delete(url);
  }
};

const cancelAllRequests = () => {
  controllers.forEach(controller => controller.abort());
  controllers.clear();
};

const apiService = {
  get,
  post,
  cancelRequest,
  cancelAllRequests,
};

export default apiService;
