import React, { useState, useEffect, useContext } from 'react';
import { CSVLink } from 'react-csv';

import RefreshIcon from '@mui/icons-material/Refresh';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import SaveAltIcon from '@mui/icons-material/SaveAlt';
import MetricsSection from '../../../../components/MetricsSection';
import AddToCampaign from '../../../../components/AddToCampaign';
import { ToasterContext } from '../../../../Context/toaster';
import { LoadingContext } from '../../../../Context/loading';
import apiService from '../../../../utils/service';
import ErrorAlert from '../../../../components/ErrorAlert';
import AdvanceMetrics from '../../../../components/AdvanceMetrics';
import TopPosts from '../../../../components/TopPosts';
import CacheDataWarning from '../../../../components/CacheDataWarning';
import styles from './OptionsPage.module.css';
import AddPostToCampaign from '../../../../components/AddPostToCampaign';
import ErrorPostsDetails from '../ErrorPostsDetails';
import { useCacheableFetch } from '../../../../hooks/useCacheableFetch';

const OptionsPage = (props) => {
  const { currCookie, username, isAllowedToRequest } = props;

  const [metricsData, setMetricsData] = useState(null);
  const [videoData, setVideoData] = useState([]);
  const [campaigns, setCampaigns] = useState(null);
  const [errorMsg, setErrorMsg] = useState('');
  const [activeTab, setActiveTab] = useState('metrics');
  const [advancedMetricsData, setAdvancedMetricsData] = useState({
    data: null,
    loading: false,
    error: false
  });
  const [posts, setPosts] = useState(null);
  const [errorCode, setErrorCode] = useState('');
  const [isErrorDetail, setIsErrorDetail] = useState(false);
  const [errorPosts, setErrorPosts] = useState([]);

  const { showToaster } = useContext(ToasterContext);
  const { loading, setLoading } = useContext(LoadingContext);

  const headers = [
    { label: 'Videos/Shorts URL', key: 'URL' },
    { label: 'Views/Play', key: 'Views_Play' }
  ];

  const formattedClipboardData = () => {
    const headerRow = ['Videos/Shorts URL', 'Views/Play'];

    const dataRows = videoData.map((item) => [item.url, item.views]);

    const allRows = [headerRow, ...dataRows];

    const formattedData = allRows.map((row) => row.join('\t')).join('\n');
    navigator.clipboard
      .writeText(formattedData)
      .then(() => {
        showToaster({
          severity: 'success',
          show: true,
          message: 'Data copied to clipboard'
        });
      })
      .catch((error) => {
        showToaster({
          severity: 'error',
          show: true,
          message: 'Failed to copy data to clipboard'
        });
      });
  };
  const handleFetchSuccess = ({
    metricsData: newMetricsData,
    advancedMetricsData: newAdvancedMetricsData,
  }) => {
    setMetricsData(newMetricsData);
    setErrorMsg('');
    setErrorCode('');

    if (newMetricsData.data.errorPosts?.length) {
      setErrorPosts(newMetricsData.data.errorPosts);
    } else {
      setErrorPosts([]);
    }

    // Update advanced metrics state if it was fetched
    if (newAdvancedMetricsData) {
      setAdvancedMetricsData({
        data: newAdvancedMetricsData,
        loading: false,
        error: false
      });
    }
  };

  const { fetchWithCache, isCacheData } = useCacheableFetch({
    currCookie,
    onSuccess: handleFetchSuccess,
    useAdvancedMetrics: true
  });

  const postScrapedData = async (reqBody) => {
    setLoading(true);
    try {
      await fetchWithCache(reqBody);
    } catch (error) {
      const errorMessage =
        error.response?.data?.errors[0]?.detail ||
        'Extension API error. Please try again.';
      const errorCode = error.response?.data?.errors[0]?.code || '';
      setErrorCode(errorCode);

      if (error.code === 'ERR_CANCELED') {
        setErrorMsg('Aborting ongoing request. Please wait...');
      } else {
        setErrorMsg(errorMessage);
      }
    } finally {
      setLoading(false);
    }
  };

  const formatViewsNumber = (views) => {
    if (typeof views === 'number') return views;
    if (typeof views !== 'string' || !views.trim()) return 0;

    const lowerViews = views.trim().toUpperCase();

    if (lowerViews.endsWith('K')) {
      return Math.round(parseFloat(lowerViews) * 1000);
    } else if (lowerViews.endsWith('M')) {
      return Math.round(parseFloat(lowerViews) * 1000000);
    } else if (lowerViews.endsWith('B')) {
      return Math.round(parseFloat(lowerViews) * 1000000000);
    } else {
      const numericValue = parseFloat(lowerViews);
      return isNaN(numericValue) ? 0 : Math.round(numericValue);
    }
  };

  const scrapeTiktokData = () => {
    // eslint-disable-next-line no-undef
    chrome.tabs.query({ active: true, currentWindow: true }, (tabs) => {
      // eslint-disable-next-line no-undef
      chrome.tabs.sendMessage(
        tabs[0].id,
        { action: 'scrapeTiktok' },
        (response) => {
          console.log(response);
        }
      );
    });
    setLoading(true);
    setErrorPosts([]);
    setMetricsData(null);
    setVideoData([]);
    setTimeout(() => {
      // eslint-disable-next-line no-undef
      chrome.storage.local.get('tiktokVids', (result) => {
        setVideoData(result.tiktokVids);
        const options = {
          username,
          platform: 'tiktok',
          posts: result.tiktokVids.map((item) => ({
            url: item.url,
            playCount: formatViewsNumber(item.views),
            thumbnailUrl: item.thumbnailUrl
          }))
        };

        let postData = result.tiktokVids.map((post) => ({
          url: post.url,
          thumbnail: post.thumbnailUrl
        }));
        setPosts(postData);
        postScrapedData(options);
      });
    }, 5000);
  };

  const fetchCampaigns = async () => {
    try {
      const response = await apiService.get(
        `${process.env.REACT_APP_API_URL}/campaign/v1/list`,
        {
          headers: {
            Authorization: `Bearer ${currCookie}`
          }
        }
      );

      setCampaigns(response.data.data);
    } catch (error) {
      const errorMessage =
        error.response.data?.errors[0].detail || 'Error fetching campaigns.';

      showToaster({
        severity: 'error',
        show: true,
        message: errorMessage
      });
    }
  };

  useEffect(() => {
    if (username && isAllowedToRequest) {
      if (loading) {
        apiService.cancelAllRequests();
        setLoading(false);
      }

      scrapeTiktokData();
    }
  }, [username, isAllowedToRequest]);

  useEffect(() => {
    fetchCampaigns();
  }, []);

  return (
    <>
      {errorMsg && errorCode !== 'TKT0004' ? (
        <ErrorAlert errorMsg={errorMsg} />
      ) : (
        <div className={styles['main-cont']}>
          <section className={styles['top-card']}>
            <div className={styles['left-box']}>
              <div className={styles['url-cx']}>
                {username && (
                  <>
                    <h3>Username:</h3>
                    <p>{username}</p>
                  </>
                )}
              </div>
            </div>
            <div className={styles['right-box']}>
              <div className={styles['button-box']}>
                <>
                  <div
                    className={styles['refresh-cta']}
                    onClick={scrapeTiktokData}
                  >
                    <RefreshIcon style={{ color: '#500b75', height: '20px' }} />
                    <span>Refresh</span>
                  </div>
                </>
                {videoData && videoData.length > 0 && (
                  <>
                    <button onClick={formattedClipboardData}>
                      <span>Copy Data to Clipboard</span>
                      <ContentCopyIcon
                        style={{ color: '#ffffff', height: '16px' }}
                      />
                    </button>
                    <CSVLink
                      data={videoData.map((item) => ({
                        URL: item.url,
                        Views_Play: item.views
                      }))}
                      headers={headers}
                      filename={`${username}_tiktok_data.csv`}
                    >
                      <span>Save Data to CSV</span>
                      <SaveAltIcon
                        style={{ color: '#500b75', height: '16px' }}
                      />
                    </CSVLink>
                  </>
                )}
              </div>
            </div>
          </section>
          {errorMsg && (
            <div className={styles['error-box']}>
              <p>{errorMsg}</p>
            </div>
          )}
          <section className={styles['tabs-section']}>
            <div className={styles['tabs-container']}>
              <button
                onClick={() => setActiveTab('metrics')}
                style={
                  activeTab === 'metrics'
                    ? { color: '#500b75' }
                    : { color: '#000000' }
                }
                type='button'
              >
                Metrics
              </button>
              <button
                onClick={() => setActiveTab('advanced-metrics')}
                style={
                  activeTab === 'advanced-metrics'
                    ? { color: '#500b75' }
                    : { color: '#000000' }
                }
                type='button'
              >
                Advanced Metrics
              </button>
              <button
                onClick={() => setActiveTab('top-posts')}
                style={
                  activeTab === 'top-posts'
                    ? { color: '#500b75' }
                    : { color: '#000000' }
                }
                type='button'
              >
                Top 6 Videos
              </button>
              <button
                onClick={() => setActiveTab('add-post')}
                style={
                  activeTab === 'add-post'
                    ? { color: '#500b75' }
                    : { color: '#000000' }
                }
                type='button'
              >
                Add Post
              </button>
            </div>
          </section>
          {isCacheData && (
            <section className={styles['bottom-card']}>
              <CacheDataWarning />
            </section>
          )}
          {metricsData && metricsData.data && activeTab === 'metrics' ? (
            <section className={styles['bottom-card']}>
              <MetricsSection
                data={metricsData.data}
                type='tiktok'
                totalCalculatedPosts={metricsData.data.posts.length}
              />
            </section>
          ) : (
            ''
          )}
          {errorPosts && errorPosts.length && activeTab === 'metrics' ? (
            <section className={styles['bottom-card']}>
              <ErrorPostsDetails
                errorPosts={errorPosts}
                isOpen={isErrorDetail}
                setIsOpen={setIsErrorDetail}
              />
            </section>
          ) : (
            ''
          )}
          {activeTab === 'advanced-metrics' ? (
            advancedMetricsData.loading ? (
              <section className={styles['advanced-metrics-section']}>
                <p className={styles['section-placeholder']}>
                  Fetching data...
                </p>
              </section>
            ) : advancedMetricsData.error ? (
              <section className={styles['advanced-metrics-section']}>
                <p className={styles['section-placeholder']}>
                  No data available
                </p>
              </section>
            ) : advancedMetricsData.data ? (
              <section className={styles['advanced-metrics-section']}>
                <AdvanceMetrics
                  data={advancedMetricsData.data}
                  type='tiktok'
                  frequencyChartId='tiktok-frequency-chart'
                  erRateHistoryChartId='tiktok-er-history-chart'
                  totalCalculatedPosts={metricsData.data.posts.length}
                />
              </section>
            ) : (
              ''
            )
          ) : (
            ''
          )}
          {activeTab === 'top-posts' ? (
            advancedMetricsData.loading ? (
              <section className={styles['top-posts-section']}>
                <p className={styles['section-placeholder']}>
                  Fetching data...
                </p>
              </section>
            ) : advancedMetricsData.error ? (
              <section className={styles['top-posts-section']}>
                <p className={styles['section-placeholder']}>
                  No data available
                </p>
              </section>
            ) : advancedMetricsData.data ? (
              <section className={styles['top-posts-section']}>
                <section className={styles['top-posts-section']}>
                  <TopPosts
                    data={advancedMetricsData.data}
                    profilePic={metricsData.data.avatar}
                    type='tiktok'
                    totalCalculatedPosts={metricsData.data.posts.length}
                  />
                </section>
              </section>
            ) : (
              ''
            )
          ) : (
            ''
          )}
          {posts && posts.length && activeTab === 'add-post' && campaigns ? (
            <section className={styles['top-posts-section']}>
              <section className={styles['top-posts-section']}>
                <AddPostToCampaign
                  campaigns={campaigns}
                  posts={posts}
                  currCookie={currCookie}
                  type='tiktok'
                />
              </section>
            </section>
          ) : (
            ''
          )}
          {metricsData && (
            <section className={styles['middle-card']}>
              <AddToCampaign
                currCookie={currCookie}
                metricsData={metricsData}
                username={username}
                campaigns={campaigns}
                type='tiktok'
              />
            </section>
          )}
        </div>
      )}
    </>
  );
};

export default OptionsPage;
