import { useState, useContext, useEffect } from 'react';
import styles from './AddPostToCampaign.module.css';
import apiService from '../../utils/service';
import { ToasterContext } from '../../Context/toaster';
import { LoadingContext } from '../../Context/loading';
import AddCircleOutlineIcon from '@mui/icons-material/AddCircleOutline';
import OpenInNewIcon from '@mui/icons-material/OpenInNew';

const AddPostToCampaign = (props) => {
  const { campaigns, posts, currCookie, type, username = '' } = props;
  const [selectedCampaignId, setSelectedCampaignId] = useState(null);
  const [updatedPosts, setUpdatedPosts] = useState(null)

  const { showToaster } = useContext(ToasterContext);
  const { setLoading } = useContext(LoadingContext);

  const handleDropdownChange = (event) => {
    setSelectedCampaignId(event.target.value);
  };

  const generateThumbnail = async (postUrl) => {
    try {
      const apiData = await apiService.get(
        `${process.env.REACT_APP_API_URL}/embed/v1/get?uri=${postUrl}`,
        {
          headers: {
            Authorization: `Bearer ${currCookie}`,
          },
        }
      );
  
      if (apiData.data) {
        return apiData.data.data.thumbnail.url;
      }
    } catch (error) {
      return (
        '/images/no_image.png'
      );
    }

  };
  const updateInstagramThumbnail = async () => {
    const processedPosts = await Promise.all(
      posts.map(async (post) => {
        const thumbnailUrl = await generateThumbnail(post.url);
        return { ...post, thumbnail: thumbnailUrl };
      }),
    );
    setUpdatedPosts(processedPosts);
  }
  const filterTiktokSkeletonImg = () => {
    const formattedData = posts.map((post) => {
      if (post.thumbnail === '') {
        return {
          ...post,
          thumbnail: '/images/no_image.png'
        }
      } else {
        return post
      }
    })
    setUpdatedPosts(formattedData)
  }

  const handleAddPost = async (url) => {
    if (!selectedCampaignId) {
      showToaster({
        severity: "error",
        show: true,
        message: "Please select campaign",
      });
      return
    }

    setLoading(true)
    const postBody = {
      campaignID: Number(selectedCampaignId),
      addedBy: 'brand',
      brandIdentifier: '',
      campaignName: '',
      mediaList: [
        {
          URL: url,
          username
        }
      ],
    };

    try {
      await apiService.post(
        `${process.env.REACT_APP_API_URL}/campaign/v1/media/add`,
        postBody,
        {
          headers: {
            Authorization: `Bearer ${currCookie}`,
          },
        }
      );

      showToaster({
        severity: "success",
        show: true,
        message: "Post added to campaign",
      });

    } catch (error) {
      if (error.response.data?.errors[0].detail) {
        showToaster({
          severity: "error",
          show: true,
          message: error.response.data?.errors[0].detail,
        });
      } else {
        showToaster({
          severity: "error",
          show: true,
          message: "Couldn't add post to campaign, please try again!",
        });
      }
    }
    setLoading(false);
  };

  useEffect(() => {
    if (type === 'instagram') {
      updateInstagramThumbnail()
    } else if (type === 'tiktok') {
      filterTiktokSkeletonImg()
    } else {
      setUpdatedPosts(posts)
    }
  }, [type])

  return (
    <div className={styles['add-post-container']}>
      <h3>Add Post to Campaign</h3>

      <div className={styles['form-container']}>
        <div className={styles['campaign-options']}>
          <select value={selectedCampaignId} onChange={handleDropdownChange}>
            <option value={null}>Select Campaign</option>
            {campaigns.map((item) => (
              <option key={item.id} value={item.id}>
                {item.slug}
              </option>
            ))}
          </select>
        </div>

        <div className={styles['post-list-title']}>Post List</div>
        <table className={styles['post-table']}>
          <thead className={styles['post-table-head']}>
            <tr>
              <th>Post</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody>
            {updatedPosts && updatedPosts.length && updatedPosts.map((post, index) => (
              <tr key={index}>
                <td>
                  <div className={styles['post-detail']}>
                    <img src={post.thumbnail || '/images/no_image.png'} alt='thumbnail' />
                    <a
                      href={post.url}
                      target='_blank'
                      rel='noreferrer'
                      className={styles['post-link']}
                    >
                      <span>{post.url}</span>
                      <OpenInNewIcon style={{ width: '16px', height: '16px' }} />
                    </a>
                  </div>
                </td>
                <td>
                  <button className={styles['post-button']} onClick={() => handleAddPost(post.url)}>
                    <AddCircleOutlineIcon />
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>

      </div>
    </div>
  );
};

export default AddPostToCampaign;
