import React, { useEffect, useState } from 'react';

import OptionsPage from './components/OptionsPage';
import InvalidUrl from '../InvalidUrl';

const LinkedinPage = (props) => {
  const { currentUrl = '', currCookie = null, isAllowedToRequest } = props;

  const [username, setUsername] = useState(null);
  const [errorMsg, setErrorMsg] = useState('');
  const [urlType, setUrlType] = useState('');
  const [isUrlInvalid, toggleInvalidUrl] = useState(null);

  function checkLinkedInURL(url) {
    // Define the regex patterns for company, profile, and recent-activity URLs
    const companyPattern =
      /^https:\/\/www\.linkedin\.com\/company\/([\w-]+)\/?(people|jobs|posts|about)?\/?(\?[\w=&]+)?$/;
    const profilePattern = /^https:\/\/www\.linkedin\.com\/in\/([\w-]+)\/?$/;
    const recentActivityPattern =
      /^https:\/\/www\.linkedin\.com\/in\/([\w-]+)\/recent-activity\/all\/?$/;

    // Test the URL against the company pattern
    let match = url.match(companyPattern);
    if (match) {
      const companyName = match[1];
      const pageType = match[2] || 'index'; // Default to 'index' if page type is not specified
      const queryParams = match[3] || ''; // Capture query parameters if present
      return {
        isValid: true,
        urlType: 'company',
        companyName: companyName,
        pageType: pageType,
        queryParams: queryParams
      };
    }

    // Test the URL against the recent-activity pattern
    match = url.match(recentActivityPattern);
    if (match) {
      const profilePath = match[1];
      return {
        isValid: true,
        urlType: 'recent-activity',
        profilePath: profilePath
      };
    }

    // Test the URL against the profile pattern
    match = url.match(profilePattern);
    if (match) {
      const profilePath = match[1];
      return {
        isValid: true,
        urlType: 'profile',
        profilePath: profilePath
      };
    }

    // If neither pattern matches, return invalid
    return {
      isValid: false,
      urlType: null,
      companyName: null,
      pageType: null,
      queryParams: null,
      profilePath: null
    };
  }

  useEffect(() => {
    const { isValid, urlType, profilePath } = checkLinkedInURL(currentUrl);

    if (isValid && (urlType === 'profile' || urlType === 'recent-activity')) {
      setUrlType(urlType);
      setUsername(profilePath);
    } else {
      toggleInvalidUrl(true);
      setUsername(null);
    }
  }, [currentUrl]);

  return (
    <>
      {isUrlInvalid ? (
        <InvalidUrl />
      ) : (
        <OptionsPage
          currCookie={currCookie}
          username={username}
          errorMsg={errorMsg}
          setErrorMsg={setErrorMsg}
          urlType={urlType}
          isAllowedToRequest={isAllowedToRequest}
        />
      )}
    </>
  );
};

export default LinkedinPage;
