import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import OptionsPageYoutube from "./components/OptionsPage";
import InvalidUrl from "../InvalidUrl";

const YoutubePage = (props) => {
  const { currentUrl = "", currCookie = null, isAllowedToRequest } = props;

  const [ytUsername, setYtUsername] = useState("");
  const [isUrlInvalid, toggleInvalidUrl] = useState(null);
  const [urlType, setUrlType] = useState('')

  const location = useLocation();
  const queryParams = new URLSearchParams(location.search);
  const queryType = queryParams.get("type");
  const queryUsername = queryParams.get("username");

  const checkYoutubeUrlCategory = (url) => {
    const parsedUrl = new URL(url);
  
    // Ensure the base URL is YouTube and it's using @username format
    if (parsedUrl.hostname !== 'www.youtube.com' || !parsedUrl.pathname.startsWith('/@')) {
      return '';
    }
  
    // Extract the part after the @username
    const path = parsedUrl.pathname.split('/@')[1].split('/');
  
    // If only `@username` with no additional path, categorize as 'user'
    if (path.length === 1 || (path.length === 2 && path[1] === '')) {
      return 'user';
    }
  
    switch (path[1]) {
      case 'shorts':
        return 'shorts';
      case 'videos':
        return 'videos';
      default:
        return 'others'; // Any other path after @username
    }
  };
  const handleYouTubeURL = () => {
    if (currentUrl.includes("https://www.youtube.com/@")) {
      const urlCategory = checkYoutubeUrlCategory(currentUrl)
      if (urlCategory) {
        setUrlType(urlCategory)
        toggleInvalidUrl(false)
        extractUsername(currentUrl);
      }
    } else {
      toggleInvalidUrl(true)
    }
  };
  const extractUsername = (theUrl) => {
    const usernameRegex = /@([^\/]*)/;
    const usernameMatch = theUrl.match(usernameRegex);
    const username = usernameMatch && usernameMatch[1];
    const lowercaseUsername = username && username.toLowerCase();
    setYtUsername(lowercaseUsername);
  };

  useEffect(() => {
    if (currentUrl && currentUrl.includes("youtube.com")) {
      handleYouTubeURL();
    } else if (queryType && queryUsername && queryType === "youtube") {
      toggleInvalidUrl(false)
      setYtUsername(queryUsername);
    }
  }, [currentUrl]);

  return (
    <>
      {isUrlInvalid ? (
        <InvalidUrl />
      ) : (
        <OptionsPageYoutube
          currCookie={currCookie}
          username={ytUsername}
          urlType={urlType}
          type="youtube"
          isAllowedToRequest={isAllowedToRequest}
        />
      )}
    </>
  );
};

export default YoutubePage;
