import React, { useEffect, useState } from "react";

import OptionsPage from "./components/OptionsPage";
import InvalidUrl from "../InvalidUrl";

const TiktokPage = (props) => {
  const { currentUrl = "", currCookie = null, isAllowedToRequest } = props;

  const [username, setUsername] = useState(null);
  const [isUrlInvalid, toggleInvalidUrl] = useState(null);

  const extractUsername = (url) => {
    const pattern = /@([^\/]*)/;
    const match = url.match(pattern);
    if (match && match[1]) {
      if (match[1].includes('?')) {
        setUsername(match[1].split('?')[0])
      } else {
        setUsername(match[1])
      }
    }
  }

  const handleUrl = () => {
    if (currentUrl && currentUrl.includes('https://www.tiktok.com/@')) {
      extractUsername(currentUrl)
    }
  }
  
  const isValidTikTokUrl = (url) => {
    const regex = /^https:\/\/www\.tiktok\.com\/@[\w.-]+(?:\?lang=[a-z]{2})?$/;
    return regex.test(url);  
  }

  useEffect(() => {
    if (isValidTikTokUrl(currentUrl)) {
      handleUrl()
      toggleInvalidUrl(false)
    } else {
      toggleInvalidUrl(true);
    }
  }, [currentUrl])


  return (
    <>
      {isUrlInvalid ? (
        <InvalidUrl />
      ) : (
        <OptionsPage
          currCookie={currCookie}
          username={username}
          isAllowedToRequest={isAllowedToRequest}
        />
      )}
    </>
  );
};

export default TiktokPage;
