import React, { useState, useEffect, useContext } from 'react';
import { CSVLink } from 'react-csv';
import {
  formatISO,
  subDays,
  subHours,
  subMinutes,
  subMonths,
  subSeconds,
  subWeeks,
  subYears
} from 'date-fns';

import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import SaveAltIcon from '@mui/icons-material/SaveAlt';
import RefreshIcon from '@mui/icons-material/Refresh';
import OpenInNewIcon from '@mui/icons-material/OpenInNew';
import MetricsSection from '../../../../components/MetricsSection';
import AddToCampaign from '../../../../components/AddToCampaign';
import apiService from '../../../../utils/service';
import ErrorAlert from '../../../../components/ErrorAlert';
import { LoadingContext } from '../../../../Context/loading';
import { ToasterContext } from '../../../../Context/toaster';
import AdvanceMetrics from '../../../../components/AdvanceMetrics';
import TopPosts from '../../../../components/TopPosts';
import AddPostToCampaign from '../../../../components/AddPostToCampaign';
import CacheDataWarning from '../../../../components/CacheDataWarning';
import { useCacheableFetch } from '../../../../hooks/useCacheableFetch';
import styles from './OptionsPage.module.css';

const OptionsPageYoutube = (props) => {
  const {
    currCookie = null,
    username = '',
    type = 'youtube',
    urlType = '',
    isAllowedToRequest
  } = props;

  const [status, setStatus] = useState('');
  const [metricsData, setMetricsData] = useState(null);
  const [postsData, setPostsData] = useState([]);
  const [errorMsg, setErrorMsg] = useState('');
  const [campaigns, setCampaigns] = useState(null);
  const [activeTab, setActiveTab] = useState('metrics');
  const [advancedMetricsData, setAdvancedMetricsData] = useState({
    data: null,
    loading: false,
    error: false
  });
  const [posts, setPosts] = useState(null);

  const { loading, setLoading } = useContext(LoadingContext);
  const { showToaster } = useContext(ToasterContext);

  const handleFetchSuccess = ({
    metricsData: newMetricsData,
    advancedMetricsData: newAdvancedMetricsData,
    fromCache
  }) => {
    setMetricsData(newMetricsData);

    // Handle different types of responses
    if (urlType === 'videos') {
      setPostsData(newMetricsData.data?.posts);
      let postData = newMetricsData.data.posts.map((post) => ({
        thumbnail: post.mediaUrl,
        url: post.url
      }));
      setPosts(postData);
    } else if (urlType === 'shorts') {
      setPostsData(newMetricsData.data?.shorts.posts);
      let postData = newMetricsData.data.shorts.posts.map((post) => ({
        thumbnail: post.mediaUrl,
        url: post.url
      }));
      setPosts(postData);
    }

    setErrorMsg('');

    if (newAdvancedMetricsData) {
      setAdvancedMetricsData({
        data: newAdvancedMetricsData,
        loading: false,
        error: false
      });
    }
  };

  const { fetchWithCache, isCacheData } = useCacheableFetch({
    currCookie,
    onSuccess: handleFetchSuccess,
    useAdvancedMetrics: true
  });

  const headers = [
    { label: 'Videos/Shorts URL', key: 'URL' },
    { label: 'Likes', key: 'Likes' },
    { label: 'Comments', key: 'Comments' },
    { label: 'Views/Play', key: 'Views_Play' }
  ];

  const formattedClipboardData = () => {
    let headerRow;
    let dataRows;

    headerRow = ['Videos/Shorts URL', 'Likes', 'Comments', 'Views/Play'];
    dataRows = postsData.map((theItem) => [
      theItem?.url,
      theItem?.likeCount,
      theItem?.commentCount,
      theItem?.playCount
    ]);

    const allRows = [headerRow, ...dataRows];

    const formattedData = allRows.map((row) => row.join('\t')).join('\n');
    navigator.clipboard
      .writeText(formattedData)
      .then(() => {
        showToaster({
          severity: 'success',
          show: true,
          message: 'Data copied to clipboard'
        });
      })
      .catch((error) => {
        showToaster({
          severity: 'error',
          show: true,
          message: 'Failed to copy data to clipboard'
        });
      });
  };

  const postScrapedData = async (reqBody) => {
    setLoading(true);
    try {
      await fetchWithCache(reqBody);
    } catch (error) {
      const errorMessage =
        error.response?.data?.errors[0]?.detail ||
        'Extension API error. Please try again.';

      if (error.code === 'ERR_CANCELED') {
        setErrorMsg('Aborting ongoing request. Please wait...');
      } else {
        setErrorMsg(errorMessage);
      }
    }
    setLoading(false);
    setStatus('refresh');
  };

  const fetchCampaigns = async () => {
    try {
      const response = await apiService.get(
        `${process.env.REACT_APP_API_URL}/campaign/v1/list`,
        {
          headers: {
            Authorization: `Bearer ${currCookie}`
          }
        }
      );

      setCampaigns(response.data.data);
    } catch (error) {
      const errorMessage =
        error.response.data?.errors[0].detail || 'Error fetching campaigns.';

      showToaster({
        severity: 'error',
        show: true,
        message: errorMessage
      });
    }
  };
  const formatNumbers = (views) => {
    if (typeof views === 'number') return views;
    if (typeof views !== 'string' || !views.trim()) return 0;

    const lowerViews = views.trim().toUpperCase();

    if (lowerViews.endsWith('K')) {
      return Math.round(parseFloat(lowerViews) * 1000);
    } else if (lowerViews.endsWith('M')) {
      return Math.round(parseFloat(lowerViews) * 1000000);
    } else if (lowerViews.endsWith('B')) {
      return Math.round(parseFloat(lowerViews) * 1000000000);
    } else {
      const numericValue = parseFloat(lowerViews);
      return isNaN(numericValue) ? 0 : Math.round(numericValue);
    }
  };
  const getEstimatedDate = (dateStr) => {
    if (!dateStr) {
      return formatISO(new Date(), { representation: 'complete' });
    }

    const now = new Date();
    const splitDateStr = dateStr.split(' ');
    const value = parseInt(splitDateStr[0] || '0', 10); // Get the numeric value
    const unit = splitDateStr[1]; // Get the unit part (e.g., minutes, hours, days, weeks, months, years)
    let pastDate;

    if (unit.includes('second')) {
      pastDate = subSeconds(now, value);
    } else if (unit.includes('minute')) {
      pastDate = subMinutes(now, value);
    } else if (unit.includes('hour')) {
      pastDate = subHours(now, value);
    } else if (unit.includes('day')) {
      pastDate = subDays(now, value);
    } else if (unit.includes('week')) {
      pastDate = subWeeks(now, value);
    } else if (unit.includes('month')) {
      pastDate = subMonths(now, value);
    } else if (unit.includes('year')) {
      pastDate = subYears(now, value);
    } else {
      pastDate = new Date();
    }

    if (pastDate === '') {
      return formatISO(new Date(), { representation: 'complete' });
    } else {
      return formatISO(pastDate, { representation: 'complete' });
    }
  };
  const getYoutubeVideosData = (refresh = false) => {
    // eslint-disable-next-line no-undef
    chrome.tabs.query({ active: true, currentWindow: true }, (tabs) => {
      // eslint-disable-next-line no-undef
      chrome.tabs.sendMessage(
        tabs[0].id,
        { action: 'scrapeYoutubeVideos', isRefresh: refresh },
        (response) => {}
      );
    });

    setTimeout(() => {
      // eslint-disable-next-line no-undef
      chrome.storage.local.get('youtubeVideos', (result) => {
        const formattedVideoData = {
          ...result.youtubeVideos,
          username,
          subscribers: formatNumbers(result.youtubeVideos.subscribers),
          totalVideos: formatNumbers(result.youtubeVideos.totalVideos),
          videos: result.youtubeVideos.videos.map((vid) => ({
            url: vid.url,
            title: vid.title,
            thumbnailUrl: vid.thumbnailUrl || '',
            views: vid.views ? formatNumbers(vid.views) : 0,
            estimatedDate: getEstimatedDate(vid.createdDate)
          }))
        };
        const payload = {
          username: username,
          platform: 'youtube',
          name: result.youtubeVideos.name,
          followers: formatNumbers(result.youtubeVideos.subscribers),
          avatar: result.youtubeVideos.avatar,
          description: result.youtubeVideos.bio,
          totalPost: formatNumbers(result.youtubeVideos.totalVideos),
          type: 'videos',
          posts: formattedVideoData.videos.slice(0, 25).map((vid) => ({
            url: vid.url,
            caption: vid.title,
            thumbnailUrl: vid.thumbnailUrl,
            playCount: vid.views,
            date: vid.estimatedDate
          }))
        };

        postScrapedData(payload);
      });
    }, 3500);
  };
  const getYoutubeShortsData = () => {
    // eslint-disable-next-line no-undef
    chrome.tabs.query({ active: true, currentWindow: true }, (tabs) => {
      // eslint-disable-next-line no-undef
      chrome.tabs.sendMessage(
        tabs[0].id,
        { action: 'scrapeYoutubeShorts' },
        (response) => {}
      );
    });
    setTimeout(() => {
      // eslint-disable-next-line no-undef
      chrome.storage.local.get('youtubeShorts', (result) => {
        const formattedShortsData = {
          ...result.youtubeShorts,
          username,
          subscribers: formatNumbers(result.youtubeShorts.subscribers),
          totalVideos: formatNumbers(result.youtubeShorts.totalVideos),
          shorts: result.youtubeShorts.shorts.map((vid) => ({
            url: vid.url,
            title: vid.title,
            thumbnailUrl: vid.thumbnailUrl || '',
            views: vid.views ? formatNumbers(vid.views) : 0
          }))
        };
        const payload = {
          username: username,
          platform: 'youtube',
          name: result.youtubeShorts.name,
          followers: formatNumbers(result.youtubeShorts.subscribers),
          avatar: result.youtubeShorts.avatar,
          description: result.youtubeShorts.bio,
          totalPost: formatNumbers(result.youtubeShorts.totalVideos),
          type: 'shorts',
          posts: formattedShortsData.shorts.slice(0, 25).map((short) => ({
            url: short.url,
            caption: short.title,
            thumbnailUrl: short.thumbnailUrl,
            playCount: short.views,
            date: short.estimatedDate
          }))
        };
        postScrapedData(payload);
      });
    }, 3500);
  };
  const extractYoutubeByUrlType = (refresh = false) => {
    setLoading(true);
    setMetricsData(null);

    if (urlType === 'videos') {
      getYoutubeVideosData(refresh);
    } else if (urlType === 'shorts') {
      getYoutubeShortsData(refresh);
    } else if (urlType === 'user' || urlType === 'others') {
      setLoading(false);
    }
  };
  const openYoutubeTab = (type) => {
    // eslint-disable-next-line no-undef
    chrome.tabs.query({ active: true, currentWindow: true }, function (tabs) {
      const activeTab = tabs[0];
      if (activeTab && activeTab.id !== undefined) {
        let url = '';
        if (type === 'videos') {
          url = `https://www.youtube.com/@${username}/videos`;
        } else if (type === 'shorts') {
          url = `https://www.youtube.com/@${username}/shorts`;
        }
        // eslint-disable-next-line no-undef
        chrome.tabs.update(activeTab.id, {
          url
        });
      }
    });
  };

  useEffect(() => {
    if (username && urlType && isAllowedToRequest) {
      if (loading) {
        apiService.cancelAllRequests();
        setLoading(false);
      }

      extractYoutubeByUrlType();
    }
  }, [username, urlType, isAllowedToRequest]);

  useEffect(() => {
    fetchCampaigns();
  }, []);

  return (
    <>
      {errorMsg ? (
        <ErrorAlert errorMsg={errorMsg} />
      ) : (
        <div className={styles['main-cont']}>
          <section className={styles['top-card']}>
            <div className={styles['left-box']}>
              <>
                <div className={styles['url-cx']}>
                  {username && (
                    <>
                      <h3>Username:</h3>
                      <p>{username}</p>
                    </>
                  )}
                </div>
              </>
              {errorMsg && (
                <div className={styles['error-box']}>
                  <small>{errorMsg}</small>
                </div>
              )}
            </div>
            <div className={styles['right-box']}>
              <div className={styles['button-box']}>
                {status === 'refresh' && (
                  <>
                    <div
                      className={styles['refresh-cta']}
                      onClick={() => extractYoutubeByUrlType(true)}
                    >
                      <RefreshIcon
                        style={{ color: '#500b75', height: '20px' }}
                      />
                      <span>Refresh</span>
                    </div>
                  </>
                )}
                {urlType === 'videos' ? (
                  <>
                    <div
                      className={styles['refresh-cta']}
                      onClick={() => openYoutubeTab('shorts')}
                    >
                      <OpenInNewIcon
                        style={{ color: '#500b75', height: '18px' }}
                      />
                      <span>Open YouTube Shorts Page</span>
                    </div>
                    <button onClick={() => formattedClipboardData()}>
                      <span>Copy Video Data to Clipboard</span>
                      <ContentCopyIcon
                        style={{ color: '#ffffff', height: '16px' }}
                      />
                    </button>
                    <CSVLink
                      data={postsData.map((theItem) => ({
                        URL: theItem?.url,
                        Likes: theItem?.likeCount,
                        Comments: theItem?.commentCount,
                        Views_Play: theItem?.playCount
                      }))}
                      headers={headers}
                      filename={`${username}_yt_video_data.csv`}
                    >
                      <span>Save Video Data to CSV</span>
                      <SaveAltIcon
                        style={{ color: '#500b75', height: '16px' }}
                      />
                    </CSVLink>
                  </>
                ) : (
                  ''
                )}
                {urlType === 'shorts' ? (
                  <>
                    <div
                      className={styles['refresh-cta']}
                      onClick={() => openYoutubeTab('videos')}
                    >
                      <OpenInNewIcon
                        style={{ color: '#500b75', height: '18px' }}
                      />
                      <span>Open YouTube Videos Page</span>
                    </div>
                    <button onClick={() => formattedClipboardData()}>
                      <span>Copy Shorts Data to Clipboard</span>
                      <ContentCopyIcon
                        style={{ color: '#ffffff', height: '16px' }}
                      />
                    </button>
                    {metricsData ? (
                      <CSVLink
                        data={postsData.map((theItem) => ({
                          URL: theItem?.url,
                          Likes: theItem?.likeCount,
                          Comments: theItem?.commentCount,
                          Views_Play: theItem?.playCount
                        }))}
                        headers={headers}
                        filename={`${username}_yt_shorts_data.csv`}
                      >
                        <span>Save Shorts Data to CSV</span>
                        <SaveAltIcon
                          style={{ color: '#500b75', height: '16px' }}
                        />
                      </CSVLink>
                    ) : (
                      ''
                    )}
                  </>
                ) : (
                  ''
                )}
                {urlType === 'user' || urlType === 'others' ? (
                  <>
                    <div
                      className={styles['refresh-cta']}
                      onClick={() => openYoutubeTab('videos')}
                    >
                      <OpenInNewIcon
                        style={{ color: '#500b75', height: '18px' }}
                      />
                      <span>Open YouTube Videos Page</span>
                    </div>
                    <div
                      className={styles['refresh-cta']}
                      onClick={() => openYoutubeTab('shorts')}
                    >
                      <OpenInNewIcon
                        style={{ color: '#500b75', height: '18px' }}
                      />
                      <span>Open YouTube Shorts Page</span>
                    </div>
                  </>
                ) : (
                  ''
                )}
              </div>
            </div>
          </section>
          {urlType === 'videos' ? (
            <>
              <section className={styles['tabs-section']}>
                <div className={styles['tabs-container']}>
                  <button
                    onClick={() => setActiveTab('metrics')}
                    style={
                      activeTab === 'metrics'
                        ? { color: '#500b75' }
                        : { color: '#000000' }
                    }
                    type='button'
                  >
                    Metrics
                  </button>
                  <button
                    onClick={() => setActiveTab('advanced-metrics')}
                    style={
                      activeTab === 'advanced-metrics'
                        ? { color: '#500b75' }
                        : { color: '#000000' }
                    }
                    type='button'
                  >
                    Advanced Metrics
                  </button>
                  <button
                    onClick={() => setActiveTab('top-posts')}
                    style={
                      activeTab === 'top-posts'
                        ? { color: '#500b75' }
                        : { color: '#000000' }
                    }
                    type='button'
                  >
                    Top 6 Videos
                  </button>
                  <button
                    onClick={() => setActiveTab('add-post')}
                    style={
                      activeTab === 'add-post'
                        ? { color: '#500b75' }
                        : { color: '#000000' }
                    }
                    type='button'
                  >
                    Add Post
                  </button>
                </div>
              </section>
              {isCacheData && (
                <section className={styles['bottom-card']}>
                  <CacheDataWarning />
                </section>
              )}
              {metricsData && activeTab === 'metrics' ? (
                <section className={styles['bottom-card']}>
                  <MetricsSection data={metricsData.data} type='youtube-vid' />
                </section>
              ) : (
                ''
              )}
              {activeTab === 'advanced-metrics' ? (
                advancedMetricsData.loading ? (
                  <section className={styles['advanced-metrics-section']}>
                    <p className={styles['section-placeholder']}>
                      Fetching data...
                    </p>
                  </section>
                ) : advancedMetricsData.error ? (
                  <section className={styles['advanced-metrics-section']}>
                    <p className={styles['section-placeholder']}>
                      No data available
                    </p>
                  </section>
                ) : advancedMetricsData.data ? (
                  <section className={styles['advanced-metrics-section']}>
                    <AdvanceMetrics
                      data={advancedMetricsData.data}
                      type='youtube-vid'
                      frequencyChartId='yt-vid-frequency-chart'
                      erRateHistoryChartId='yt-vid-er-history-chart'
                    />
                  </section>
                ) : (
                  ''
                )
              ) : (
                ''
              )}
              {activeTab === 'top-posts' && metricsData ? (
                advancedMetricsData.loading ? (
                  <section className={styles['top-posts-section']}>
                    <p className={styles['section-placeholder']}>
                      Fetching data...
                    </p>
                  </section>
                ) : advancedMetricsData.error ? (
                  <section className={styles['top-posts-section']}>
                    <p className={styles['section-placeholder']}>
                      No data available
                    </p>
                  </section>
                ) : advancedMetricsData.data ? (
                  <section className={styles['top-posts-section']}>
                    <section className={styles['top-posts-section']}>
                      <TopPosts
                        data={advancedMetricsData.data}
                        profilePic={metricsData.data.avatar}
                        type='youtube-vid'
                      />
                    </section>
                  </section>
                ) : (
                  ''
                )
              ) : (
                ''
              )}
              {posts && posts.length && activeTab === 'add-post' ? (
                <section className={styles['top-posts-section']}>
                  <section className={styles['top-posts-section']}>
                    <AddPostToCampaign
                      campaigns={campaigns}
                      posts={posts}
                      currCookie={currCookie}
                      type='youtube'
                    />
                  </section>
                </section>
              ) : (
                ''
              )}
              {metricsData && (
                <section className={styles['middle-card']}>
                  <AddToCampaign
                    currCookie={currCookie}
                    metricsData={metricsData}
                    username={username}
                    type={type}
                    campaigns={campaigns}
                  />
                </section>
              )}
            </>
          ) : (
            ''
          )}
          {urlType === 'shorts' ? (
            <>
              <section className={styles['tabs-section']}>
                <div className={styles['tabs-container']}>
                  <button
                    onClick={() => setActiveTab('metrics')}
                    style={
                      activeTab === 'metrics'
                        ? { color: '#500b75' }
                        : { color: '#000000' }
                    }
                    type='button'
                  >
                    Metrics
                  </button>
                  <button
                    onClick={() => setActiveTab('advanced-metrics')}
                    style={
                      activeTab === 'advanced-metrics'
                        ? { color: '#500b75' }
                        : { color: '#000000' }
                    }
                    type='button'
                  >
                    Advanced Metrics
                  </button>
                  <button
                    onClick={() => setActiveTab('top-posts')}
                    style={
                      activeTab === 'top-posts'
                        ? { color: '#500b75' }
                        : { color: '#000000' }
                    }
                    type='button'
                  >
                    Top 6 Shorts
                  </button>
                  <button
                    onClick={() => setActiveTab('add-post')}
                    style={
                      activeTab === 'add-post'
                        ? { color: '#500b75' }
                        : { color: '#000000' }
                    }
                    type='button'
                  >
                    Add Post
                  </button>
                </div>
              </section>
              {isCacheData && (
                <section className={styles['bottom-card']}>
                  <CacheDataWarning />
                </section>
              )}
              {metricsData && activeTab === 'metrics' ? (
                <section className={styles['bottom-card']}>
                  <MetricsSection
                    data={metricsData.data.shorts}
                    type='youtube-shorts'
                  />
                </section>
              ) : (
                ''
              )}
              {activeTab === 'advanced-metrics' ? (
                advancedMetricsData.loading ? (
                  <section className={styles['advanced-metrics-section']}>
                    <p className={styles['section-placeholder']}>
                      Fetching data...
                    </p>
                  </section>
                ) : advancedMetricsData.error ? (
                  <section className={styles['advanced-metrics-section']}>
                    <p className={styles['section-placeholder']}>
                      No data available
                    </p>
                  </section>
                ) : advancedMetricsData.data ? (
                  <section className={styles['advanced-metrics-section']}>
                    <AdvanceMetrics
                      data={advancedMetricsData.data.shorts}
                      type='youtube-shorts'
                      frequencyChartId='yt-shorts-frequency-chart'
                      erRateHistoryChartId='yt-shorts-er-history-chart'
                    />
                  </section>
                ) : (
                  ''
                )
              ) : (
                ''
              )}
              {activeTab === 'top-posts' && metricsData ? (
                advancedMetricsData.loading ? (
                  <section className={styles['top-posts-section']}>
                    <p className={styles['section-placeholder']}>
                      Fetching data...
                    </p>
                  </section>
                ) : advancedMetricsData.error ? (
                  <section className={styles['top-posts-section']}>
                    <p className={styles['section-placeholder']}>
                      No data available
                    </p>
                  </section>
                ) : advancedMetricsData.data ? (
                  <section className={styles['top-posts-section']}>
                    <section className={styles['top-posts-section']}>
                      <TopPosts
                        data={advancedMetricsData.data.shorts}
                        profilePic={metricsData.data.avatar}
                        type='youtube-shorts'
                      />
                    </section>
                  </section>
                ) : (
                  ''
                )
              ) : (
                ''
              )}
              {posts && posts.length && activeTab === 'add-post' ? (
                <section className={styles['top-posts-section']}>
                  <section className={styles['top-posts-section']}>
                    <AddPostToCampaign
                      campaigns={campaigns}
                      posts={posts}
                      currCookie={currCookie}
                      type='youtube'
                    />
                  </section>
                </section>
              ) : (
                ''
              )}
              {metricsData && (
                <section className={styles['middle-card']}>
                  <AddToCampaign
                    currCookie={currCookie}
                    metricsData={metricsData}
                    username={username}
                    type={type}
                    campaigns={campaigns}
                  />
                </section>
              )}
            </>
          ) : (
            ''
          )}
        </div>
      )}
    </>
  );
};

export default OptionsPageYoutube;
