import { createContext, useContext, useState } from 'react';
import apiService from '../../utils/service';

const AccessContext = createContext({
  hasAccess: null,
  checkAccess: () => {}
});

export const AccessProvider = ({ children }) => {
  const [hasAccess, setHasAccess] = useState(null);

  const checkAccess = async (authToken) => {
    try {
      await apiService.get(
        `${process.env.REACT_APP_API_URL}/extension/v1/user`,
        {
          headers: {
            Authorization: `Bearer ${authToken}`
          }
        }
      );
      setHasAccess(true);
    } catch (error) {
      setHasAccess(false);
    }
  };

  return (
    <AccessContext.Provider value={{ hasAccess, checkAccess }}>
      {children}
    </AccessContext.Provider>
  );
};

export const useAccessVaildation = () => useContext(AccessContext);
