import React, { useState, useEffect, useContext } from 'react';
import { CSVLink } from 'react-csv';

import RefreshIcon from '@mui/icons-material/Refresh';
import SaveAltIcon from '@mui/icons-material/SaveAlt';
import MetricsSection from '../../../../components/MetricsSection';
import AddToCampaign from '../../../../components/AddToCampaign';
import InvalidUrl from '../../../InvalidUrl';
import { ToasterContext } from '../../../../Context/toaster';
import { LoadingContext } from '../../../../Context/loading';

import styles from './OptionsPage.module.css';
import apiService from '../../../../utils/service';
import AddPostToCampaign from '../../../../components/AddPostToCampaign';

const OptionsPage = (props) => {
  const {
    currCookie,
    username,
    errorMsg,
    setErrorMsg,
    urlType,
    isAllowedToRequest
  } = props;

  const [status, setStatus] = useState('');
  const [metricsData, setMetricsData] = useState(null);
  const [facebookPosts, setFacebookPosts] = useState([]);
  const [postsCopy, setPostsCopy] = useState([]);
  const [pageName, setPageName] = useState(null);
  const [activeTab, setActiveTab] = useState('metrics');
  const [campaigns, setCampaigns] = useState(null);
  const [posts, setPosts] = useState(null);
  const { showToaster } = useContext(ToasterContext);
  const { loading, setLoading } = useContext(LoadingContext);

  const postScrapedData = async (reqBody) => {
    setLoading(true);
    try {
      const response = await apiService.post(
        `${process.env.REACT_APP_API_URL}/scraper/v1/post`,
        {
          ...reqBody
        },
        {
          headers: {
            Authorization: `Bearer ${currCookie}`
          }
        }
      );

      if (response.data) {
        setMetricsData(response.data);
        setErrorMsg('');
      }
    } catch (error) {
      const errorMessage =
        error.response.data?.errors[0].detail ||
        'Extension API error. Please try again.';

      if (error.code === 'ERR_CANCELED') {
        setErrorMsg('Aborting ongoing request. Please wait...');
      } else {
        setErrorMsg(errorMessage);
      }
    }

    setLoading(false);
    setStatus('refresh');
  };

  const getFacebookReelsData = (refresh = false) => {
    // eslint-disable-next-line no-undef
    chrome.tabs.query({ active: true, currentWindow: true }, (tabs) => {
      // eslint-disable-next-line no-undef
      chrome.tabs.sendMessage(
        tabs[0].id,
        { action: 'scrapeFacebookReels', isRefresh: refresh },
        (response) => {
          // console.log(response);
        }
      );
    });
    setLoading(true);
    setMetricsData(null);
    setPostsCopy([]);
    setPageName(null);
    setTimeout(() => {
      // eslint-disable-next-line no-undef
      chrome.storage.local.get('facebookReels', (result) => {
        const postDataToCopy = result.facebookReels.reels.map((reel) => ({
          url: reel.reelsUrl,
          view: reel.views
        }));
        const payload = {
          username: urlType === 'reels-username' ? username : '',
          pageId: urlType === 'reels-userid' ? username : '',
          platform: 'facebook',
          name: result.facebookReels.name,
          phone: '',
          email: '',
          followers: result.facebookReels.totalFollowers,
          avatar: result.facebookReels.profilePicture,
          posts: result.facebookReels.reels.map((post) => ({
            url: post.reelsUrl,
            playCount: post.views,
            thumbnailUrl: post.thumbnail,
            mediaId: post.mediaId
          }))
        };
        const postDataForCampaign = result.facebookReels.reels.map((reel) => ({
          url: reel.reelsUrl,
          thumbnail: reel.thumbnail
        }));

        setFacebookPosts(result.facebookReels.reels);
        setPostsCopy(postDataToCopy);
        setPageName(result.facebookReels.name);
        setPosts(postDataForCampaign);
        postScrapedData(payload);
      });

      setStatus('refresh');
      setLoading(false);
    }, 10000);
  };

  const extractFacebookByUrlType = (refresh = false) => {
    if (urlType === 'groups') {
      // Handle group url type
    } else if (urlType === 'reel') {
      // Handle reel URL type
    } else if (urlType === 'reels-username' || urlType === 'reels-userid') {
      getFacebookReelsData(refresh);
    }
  };

  const fetchCampaigns = async () => {
    try {
      const response = await apiService.get(
        `${process.env.REACT_APP_API_URL}/campaign/v1/list`,
        {
          headers: {
            Authorization: `Bearer ${currCookie}`
          }
        }
      );

      setCampaigns(response.data.data);
    } catch (error) {
      const errorMessage =
        error.response.data?.errors[0].detail || 'Error fetching campaigns.';

      showToaster({
        severity: 'error',
        show: true,
        message: errorMessage
      });
    }
  };

  const headers = [
    { label: 'Reels URL', key: 'URL' },
    { label: 'Views', key: 'Views_Play' }
  ];

  useEffect(() => {
    if (username && isAllowedToRequest) {
      if (loading) {
        apiService.cancelAllRequests();
        setLoading(false);
      }

      extractFacebookByUrlType();
    }
  }, [username, isAllowedToRequest]);

  useEffect(() => {
    fetchCampaigns();
  }, []);

  return (
    <>
      {errorMsg ? (
        <InvalidUrl />
      ) : (
        <div className={styles['main-cont']}>
          <section className={styles['top-card']}>
            <div className={styles['left-box']}>
              <>
                <div className={styles['url-cx']}>
                  {pageName ? (
                    <>
                      <h3>Username:</h3>
                      <p>{pageName}</p>
                    </>
                  ) : (
                    <>
                      <h3>Username:</h3>
                      <p>{username}</p>
                    </>
                  )}
                </div>
              </>
              {errorMsg && (
                <div className={styles['error-box']}>
                  <small>{errorMsg}</small>
                </div>
              )}
            </div>
            <div className={styles['right-box']}>
              {facebookPosts && facebookPosts.length > 0 && (
                <div className={styles['button-box']}>
                  {status === 'refresh' && (
                    <>
                      <div
                        className={styles['refresh-cta']}
                        onClick={() => extractFacebookByUrlType(true)}
                      >
                        <RefreshIcon
                          style={{ color: '#500b75', height: '20px' }}
                        />
                        <span>Refresh</span>
                      </div>
                    </>
                  )}
                  <CSVLink
                    data={postsCopy.map((item) => ({
                      URL: item.url,
                      Views_Play: item.view
                    }))}
                    headers={headers}
                    filename={`${username}_facebook_data.csv`}
                  >
                    <span>Save Data to CSV</span>
                    <SaveAltIcon style={{ color: '#500b75', height: '16px' }} />
                  </CSVLink>
                </div>
              )}
            </div>
          </section>
          <section className={styles['tabs-section']}>
            <div className={styles['tabs-container']}>
              <button
                onClick={() => setActiveTab('metrics')}
                style={
                  activeTab === 'metrics'
                    ? { color: '#500b75' }
                    : { color: '#000000' }
                }
                type='button'
              >
                Metrics
              </button>
              <button
                onClick={() => setActiveTab('add-post')}
                style={
                  activeTab === 'add-post'
                    ? { color: '#500b75' }
                    : { color: '#000000' }
                }
                type='button'
              >
                Add Post
              </button>
            </div>
          </section>
          {metricsData && activeTab === 'metrics' ? (
            <section className='bottom-card'>
              <MetricsSection
                data={metricsData.data}
                type='facebook'
                postCount={postsCopy.length}
              />
            </section>
          ) : (
            ''
          )}
          {posts && posts.length && activeTab === 'add-post' ? (
            <section className={styles['top-posts-section']}>
              <section className={styles['top-posts-section']}>
                <AddPostToCampaign
                  campaigns={campaigns}
                  posts={posts}
                  currCookie={currCookie}
                  type='facebook'
                />
              </section>
            </section>
          ) : (
            ''
          )}
          {metricsData && (
            <section className={styles['middle-card']}>
              <AddToCampaign
                currCookie={currCookie}
                metricsData={metricsData}
                username={username}
                campaigns={campaigns}
                type='facebook'
              />
            </section>
          )}
        </div>
      )}
    </>
  );
};

export default OptionsPage;
