import WarningAmberRoundedIcon from '@mui/icons-material/WarningAmberRounded';
import ExpandLessIcon from '@mui/icons-material/ExpandLess';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import styles from './ErrorPostsDetails.module.css';
import OpenInNewIcon from '@mui/icons-material/OpenInNew';

const ErrorPostsDetails = (props) => {
  const { isOpen, setIsOpen, errorPosts } = props;

  return (
    <div className={styles['alert-container']}>
      <div className={styles['alert']}>
        <WarningAmberRoundedIcon style={{ width: '16px', height: '16px', color: '#e32662' }} />
        <div className={styles['alert-content']}>
          <h4 className={styles['alert-title']}>Processing Errors</h4>
          <div className={styles['alert-description']}>
            <button
              onClick={() => setIsOpen(!isOpen)}
              className={styles['toggle-button']}
            >
              <span className={styles['error-count']}>
                {errorPosts.length} posts failed to process
              </span>
              {isOpen ? (
                <ExpandLessIcon style={{ width: '16px', height: '16px' }} />
              ) : (
                <ExpandMoreIcon style={{ width: '16px', height: '16px' }} />
              )}
            </button>

            {isOpen && (
              <div className={styles['error-list']}>
                {errorPosts.map((error, index) => (
                  <div key={index} className={styles['error-item']}>
                    <a
                      href={error.url}
                      target='_blank'
                      rel='noreferrer'
                      className={styles['error-url']}
                    >
                      <span>{error.url}</span>
                      <OpenInNewIcon style={{ width: '16px', height: '16px' }} />
                    </a>
                    <p className={styles['error-message']}>
                      {error.errorMessage}
                    </p>
                  </div>
                ))}
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default ErrorPostsDetails;
