import React from 'react';
import styles from './AccessGuard.module.css';
import LoginIcon from '@mui/icons-material/Login';
import PrivacyTipIcon from '@mui/icons-material/PrivacyTip';

const AccessGuard = (props) => {
  const { hasToken, hasAccess } = props;

  const handleSignIn = () => {
    window.open(
      `${process.env.REACT_APP_PUBLIC_URL}/sign-in`,
      '_blank',
      'noopener,noreferrer'
    );
  };

  if (!hasToken) {
    return (
      <div className={styles['access-guard-container']}>
        <div>
          <div>
            <LoginIcon />
          </div>
          <h1>Sign In Required</h1>
          <p>Please sign in before accessing influencer data</p>
          <button onClick={handleSignIn}>Sign in</button>
        </div>
      </div>
    );
  } else {
    return (
      <div className={styles['access-guard-container']}>
        <div>
          {hasAccess === null ? (
            <>
              <div>
                <PrivacyTipIcon />
              </div>
              <h1>Please Wait</h1>
              <p>Checking access...</p>
            </>
          ) : (
            ''
          )}
          {hasAccess === false ? (
            <>
              <div>
                <PrivacyTipIcon />
              </div>
              <h1>Access Denied</h1>
              <p>You are not authorized to access influencer data.</p>
            </>
          ) : (
            ''
          )}
        </div>
      </div>
    );
  }
};

export default AccessGuard;
