import React from "react";
import CircularProgress from "@mui/material/CircularProgress";
import "./styles.css";

const Spinner = (props) => {
  const { loading } = props;
  return (
    <>
      {loading ? (
        <div className="wrap">
          <CircularProgress />
        </div>
      ) : (
        ""
      )}
    </>
  );
};

export default Spinner;
