import { useState } from 'react';
import apiService from '../utils/service';

export const useCacheableFetch = ({
  currCookie,
  onSuccess,
  useAdvancedMetrics = false
}) => {
  const [isCacheData, setIsCacheData] = useState(false);

  const fetchCachedMetricsData = async (cachePayload) => {
    try {
      const cacheResponse = await apiService.post(
        `${process.env.REACT_APP_API_URL}/scraper/v1/post/cache`,
        cachePayload,
        {
          headers: {
            Authorization: `Bearer ${currCookie}`
          }
        }
      );

      if (cacheResponse.data) {
        setIsCacheData(true);

        if (useAdvancedMetrics) {
          // Only fetch advanced metrics if configured and basic metrics succeeded
          const advancedMetricsResponse = await apiService.post(
            `${process.env.REACT_APP_API_URL}/scraper/v1/advance-metrics/cache`,
            cachePayload,
            {
              headers: {
                Authorization: `Bearer ${currCookie}`
              }
            }
          );

          onSuccess({
            metricsData: cacheResponse.data,
            advancedMetricsData: advancedMetricsResponse.data.data,
            fromCache: true
          });
        } else {
          onSuccess({
            metricsData: cacheResponse.data,
            fromCache: true
          });
        }
      }
    } catch (error) {
      throw new Error('Extension API error. Please try again.');
    }
  };

  const fetchWithCache = async (reqBody) => {
    setIsCacheData(false); // Reset cache flag for fresh request

    try {
      const response = await apiService.post(
        `${process.env.REACT_APP_API_URL}/scraper/v1/post`,
        reqBody,
        {
          headers: {
            Authorization: `Bearer ${currCookie}`
          }
        }
      );

      if (response.data) {
        if (useAdvancedMetrics) {
          const advancedMetricsResponse = await apiService.post(
            `${process.env.REACT_APP_API_URL}/scraper/v1/advance-metrics`,
            reqBody,
            {
              headers: {
                Authorization: `Bearer ${currCookie}`
              }
            }
          );

          onSuccess({
            metricsData: response.data,
            advancedMetricsData: advancedMetricsResponse.data.data,
            fromCache: false
          });
        } else {
          onSuccess({
            metricsData: response.data,
            fromCache: false
          });
        }
      }
    } catch (error) {
      if (error.code === 'ECONNABORTED') {
        // Fallback to cache endpoint
        const cachePayload = {
          username: reqBody.username,
          platform: reqBody.platform
        };
        await fetchCachedMetricsData(cachePayload);
      } else {
        throw error;
      }
    }
  };

  return {
    fetchWithCache,
    isCacheData
  };
};
