import React, { useContext, useState } from "react";
import CloseIcon from "@mui/icons-material/Close";
import { LoadingContext } from "../../Context/loading";
import { ToasterContext } from "../../Context/toaster";
import * as XLSX from "xlsx";

import styles from "./AddBulkInfluencer.module.css";
import apiService from "../../utils/service";

const AddBulkInfluencer = (props) => {
  const { suggestedUsers = [], currCookie = null, type = 'instagram' } = props;

  const [tags, setTags] = useState([]);
  const [inputValue, setInputValue] = useState("");
  const [multiTags, setMultiTags] = useState('');

  const { setLoading } = useContext(LoadingContext);
  const { showToaster } = useContext(ToasterContext);

  const handleAddToDirectory = async () => {
    setLoading(true);
    let data = []

    if (type === 'instagram') {
      data = suggestedUsers?.map((item) => {
        const name = item?.node?.full_name;
        const username = item?.node?.username;
        const tagsString = tags?.join(", ");
  
        return {
          Name: name,
          Email: "",
          PhoneNumber: "",
          Gender: "",
          Country: "",
          Tags: tagsString,
          Instagram: username,
          Youtube: "",
          Tiktok: "",
          Facebook: "",
        };
      });
    } else if (type === 'x') {
      data = suggestedUsers?.map((item) => {
        const name = item?.name;
        const username = item?.username;
        const tagsString = tags?.join(", ");
  
        return {
          Name: name,
          Email: "",
          PhoneNumber: "",
          Gender: "",
          Country: "",
          Tags: tagsString,
          Instagram: "",
          Youtube: "",
          Tiktok: "",
          Facebook: "",
          Twitter: username
        };
      });
    } else if (type === 'linkedin') {
      data = suggestedUsers?.map((item) => {
        const name = item?.name;
        const username = item?.username;
        const tagsString = tags?.join(", ");
  
        return {
          Name: name,
          Email: "",
          PhoneNumber: "",
          Gender: "",
          Country: "",
          Tags: tagsString,
          Instagram: "",
          Youtube: "",
          Tiktok: "",
          Facebook: "",
          Twitter: "",
          Linkedin: username
        };
      });
    }

    // Convert data to Excel format
    const worksheet = XLSX.utils.json_to_sheet(data);
    const workbook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workbook, worksheet, "Sheet1");

    // Convert workbook to blob
    const wbout = XLSX.write(workbook, { bookType: "xlsx", type: "array" });
    const blob = new Blob([wbout], { type: "application/octet-stream" });

    const formData = new FormData();
    formData.append("file", blob, "data.xlsx");

    try {
      const response = await apiService.post(
        `${process.env.REACT_APP_API_URL}/influencer/v1/import`,
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data",
            Authorization: `Bearer ${currCookie}`,
          },
        }
      );

      if (response.status === 200) {
        showToaster({
          severity: "success",
          show: true,
          message: "Suggested Influencers added to Influencer Directory",
        });
        setTags([]);
      }
    } catch (error) {
      const errorMessage = error.response.data?.errors[0].detail || 'Could not add data, please try again!'

      showToaster({
        severity: "error",
        show: true,
        message: errorMessage,
      });
    }

    setLoading(false);
  };

  const handleInputChange = (event) => {
    setInputValue(event.target.value);
  };

  const handleInputKeyDown = (event) => {
    if (event.key === "Enter") {
      event.preventDefault();
      const newTag = inputValue.trim();

      // check for empty & duplicate
      if (newTag !== "" && !tags.includes(newTag)) {
        setTags([...tags, newTag]);
        setInputValue("");
      } else {
        setInputValue("");
      }
    }
  };

  const separateString = (string) => {
    return string.split(/\s*,\s*/).map(item => item.trim());
  }

  const separateTagsByComma = () => {
    const tagsCollection = separateString(multiTags)

    // filter out duplicate from new input
    const uniqueTagsCollection = tagsCollection.filter(tag => !tags.includes(tag))

    // filter out duplicate from state & from input
    const uniqueCombinedTags = [...new Set([...tags, ...uniqueTagsCollection])];

    setTags(uniqueCombinedTags)
  }

  const handleMultiTagsEnter = (e) => {
    if (e.key === 'Enter') {
      e.preventDefault()
      separateTagsByComma()
      setMultiTags('')
    }
  }

  const handleTagDelete = (theTag) => {
    setTags(tags.filter((tag) => tag !== theTag));
  };

  return (
    <div className={styles['bulk-influencer']}>
      <h3>Add Suggested Influencers With Tags to Influencer Directory:</h3>
      <div className={styles['bulk-influencer-form-fields']}>
        <div className={styles['bulk-influencer-form-fields-item']}>
          <img src="/images/icon-tags.png" alt="Tags" />
          <input
            type="text"
            value={inputValue}
            onChange={handleInputChange}
            onKeyDown={handleInputKeyDown}
            placeholder="Add a tag and press Enter"
          />
        </div>
        <div className={styles['bulk-influencer-form-fields-item']}>
          <img src="/images/icon-tags.png" alt="Tags" />
          <input
            type="text"
            value={multiTags}
            onChange={(e) => (setMultiTags(e.target.value))}
            onKeyDown={handleMultiTagsEnter}
            placeholder="Separate tags by comma and press Enter"
          />
        </div>
        {tags && tags.length ? (
          <section className={styles['tags-section']}>
            {tags.map((tag, index) => (
              <span key={index} className={styles['single-tag']}>
                <p>{tag}</p>
                <CloseIcon
                  style={{ width: "14px", height: "14px", cursor: "pointer" }}
                  onClick={() => handleTagDelete(tag)}
                />
              </span>
            ))}
          </section>
        ) : (
          ''
        )}
        <div className={styles['bulk-influencer-form-button-wrap']}>
          <button onClick={handleAddToDirectory}>+ Add Influencers</button>
        </div>
      </div>
    </div>
  );
};

export default AddBulkInfluencer;
