import React, { useContext } from 'react';
import Snackbar from '@mui/material/Snackbar';
import Alert from '@mui/material/Alert';
import { node } from 'prop-types';

const DEFAULT_ANCHOR = {
  vertical: 'bottom',
  horizontal: 'center',
};

const ToasterContext = React.createContext({
  severity: 'info',
  open: false,
  message: '',
  showToaster: (params) => {}, // eslint-disable-line
});

const useToaster = () => {
  const context = useContext(ToasterContext);
  return context;
};

const ToasterProvider = ({ children }) => {
  const [open, updateToasterState] = React.useState(false);
  const [severity, updateSeverity] = React.useState('info');
  const [message, updateMessage] = React.useState('');
  const [anchorOrigin, setAnchorOrigin] = React.useState(DEFAULT_ANCHOR);

  const showToaster = (params) => {
    updateToasterState(params.show);
    updateSeverity(params.severity);
    updateMessage(params.message);
    setAnchorOrigin(params.anchorOrigin ? params.anchorOrigin : DEFAULT_ANCHOR);
  };

  const handleClose = () => {
    updateToasterState(false);
    updateSeverity('info');
    updateMessage('');
  };

  return (
    <ToasterContext.Provider value={{ severity, message, open, showToaster }}>
      <Snackbar
        open={open}
        autoHideDuration={4000}
        anchorOrigin={anchorOrigin}
        onClose={handleClose}
      >
        <Alert variant="filled" severity={severity}>
          {message}
        </Alert>
      </Snackbar>
      {children}
    </ToasterContext.Provider>
  );
};

ToasterProvider.propTypes = {
  children: node.isRequired,
};

export { ToasterProvider, ToasterContext, useToaster };
