import styles from './AdvanceMetrics.module.css';
import Chart from '../Charts';

const AdvanceMetrics = (props) => {
  const {
    data,
    type,
    frequencyChartId,
    erRateHistoryChartId,
    totalCalculatedPosts
  } = props;

  const chartDataFrequency = () => {
    const seriesDataFrequency = [
      {
        name: 'Posting Frequency',
        color: '#500b75',
        data: []
      }
    ];

    const xAxisFrequency = {
      title: 'Days',
      categories: [],
      labels: {
        rotation: 0,
        step: (function () {
          let dataLength = data.postingFrequency.length;
          let maxLabels = 8;
          return Math.ceil(dataLength / maxLabels);
        })()
      }
    };
    data.postingFrequency.forEach((d) => {
      const dateLabel = new Date(d.label).toLocaleDateString();
      seriesDataFrequency[0].data.push(d.value);
      xAxisFrequency.categories.push(dateLabel);
    });
    return {
      seriesDataFrequency,
      xAxisFrequency,
      yAxisFrequency: {
        min: 0,
        title: 'Values',
        allowDecimals: false
      }
    };
  };
  let seriesDataFrequency, xAxisFrequency, yAxisFrequency;
  if (data.postingFrequency) {
    const {
      seriesDataFrequency: seriesData,
      xAxisFrequency: xAxis,
      yAxisFrequency: yAxis
    } = chartDataFrequency();
    seriesDataFrequency = seriesData;
    xAxisFrequency = xAxis;
    yAxisFrequency = yAxis;
  }

  const chartDataER = () => {
    const engagementRateData = [
      {
        name: 'Average Engagement Rate History',
        color: '#BB58FF',
        data: [],
        maxPointWidth: 50
      }
    ];

    const xAxisEngagementRate = {
      title: 'Days',
      categories: [],
      labels: {
        rotation: 0,
        step: (function () {
          let dataLength = data.popularEngagementDay.length;
          let maxLabels = 8;
          return Math.ceil(dataLength / maxLabels);
        })()
      }
    };
    data.popularEngagementDay.forEach((d) => {
      engagementRateData[0].data.push(Number(d.value.toFixed(2)));
      const date = new Date(d.label);

      const day = new Intl.DateTimeFormat('en-US', { day: 'numeric' }).format(
        date
      );
      const month = new Intl.DateTimeFormat('en-US', { month: 'short' }).format(
        date
      );
      const year = new Intl.DateTimeFormat('en-US', { year: 'numeric' }).format(
        date
      );

      const dateLabel = `${day} ${month} ${year}`;
      xAxisEngagementRate.categories.push(dateLabel);
    });
    return {
      engagementRateData,
      xAxisEngagementRate,
      yAxisEngagementRate: {
        min: 0,
        title: 'Values'
      }
    };
  };
  let engagementRateData, xAxisEngagementRate, yAxisEngagementRate;
  if (data.popularEngagementDay) {
    const {
      engagementRateData: seriesData,
      xAxisEngagementRate: xAxis,
      yAxisEngagementRate: yAxis
    } = chartDataER();
    engagementRateData = seriesData;
    xAxisEngagementRate = xAxis;
    yAxisEngagementRate = yAxis;
  }

  const renderFetchMsg = (type, postsLength) => {
    switch (type) {
      case 'youtube-vid':
        return 'Data fetched for latest 20 videos';
      case 'youtube-shorts':
        return 'Data fetched for latest 20 shorts';
      case 'instagram':
        return 'Data fetched for latest 24 reels';
      case 'tiktok':
        return `Data fetched for latest ${postsLength} videos`;
      default:
        break;
    }
  };

  const renderTitle = (type) => {
    switch (type) {
      case 'youtube-vid':
        return 'Advance Metrics (Videos)';
      case 'youtube-shorts':
        return 'Advance Metrics (Shorts)';
      default:
        return 'Advance Metrics';
    }
  };

  const postType = (type) => {
    switch (type) {
      case 'instagram':
        return 'reels';
      case 'x':
        return 'posts';
      case 'linkedin':
        return 'posts';
      default:
        return 'videos';
    }
  };

  return (
    <div className={styles['chart-cx']}>
      <div className={styles['chart-cx-header']}>
        <h3>{renderTitle(type)}</h3>
        <p className={styles['small-msg']}>
          {renderFetchMsg(type, totalCalculatedPosts)}
        </p>
      </div>

      <div className={styles['full-chart-cx']}>
        <div className={styles['card-in-chart-cx']}>
          <div className={styles['full-chart-title-cx']}>
            <h3>Average Engagement Rate History</h3>
          </div>
        </div>
        <div className={styles['full-chart-area-cx']}>
          {data.popularEngagementDay ? (
            <Chart
              showTitle={false}
              type='line'
              containerId={erRateHistoryChartId}
              yAxis={yAxisEngagementRate}
              seriesData={engagementRateData}
              xAxis={xAxisEngagementRate}
            />
          ) : (
            ''
          )}
        </div>
      </div>

      <div className={`${styles['full-chart-cx']} ${styles['top-border']}`}>
        <div className={styles['card-in-chart-cx']}>
          <div className={styles['full-chart-title-cx']}>
            <h3>Posting Frequency</h3>
          </div>
          <div className={styles['single-card-in-chart-cx-wrap']}>
            <div className={styles['single-card-first-in-chart-cx']}>
              <div className={styles['info-icon-wrap-cx']}>
                <img src='/images/icon-video.png' alt='Video' />
                <p>{data.avgPostPerDay.toFixed(1)}</p>
              </div>
              <p>Avg {postType(type)} per day</p>
            </div>
            <div className={styles['single-card-first-in-chart-cx']}>
              <div className={styles['info-icon-wrap-cx']}>
                <img src='/images/icon-video.png' alt='Video' />
                <p>{data.avgPostPerWeek.toFixed(1)}</p>
              </div>
              <p>Avg {postType(type)} per week</p>
            </div>
            <div className={styles['single-card-first-in-chart-cx']}>
              <div className={styles['info-icon-wrap-cx']}>
                <img src='/images/icon-video.png' alt='Video' />
                <p>{data.avgPostPerMonth.toFixed(1)}</p>
              </div>
              <p>Avg {postType(type)} per month</p>
            </div>
          </div>
        </div>
        <div className={styles['full-chart-area-cx']}>
          {data.postingFrequency ? (
            <Chart
              showTitle={false}
              plotOptions={{
                series: {
                  borderWidth: 0,
                  dataLabels: {
                    formatter: function () {
                      /* eslint-disable */
                      return this.y != 0 ? this.y : '';
                      /* eslint-enable */
                    },
                    enabled: true
                  }
                }
              }}
              containerId={frequencyChartId}
              yAxis={yAxisFrequency}
              seriesData={seriesDataFrequency}
              xAxis={xAxisFrequency}
            />
          ) : (
            ''
          )}
        </div>
      </div>
    </div>
  );
};

export default AdvanceMetrics;
