import React, { useState, useEffect, useRef, useContext } from 'react';
import { Routes, Route } from 'react-router-dom';
import AccessGuard from '../AccessGuard';
import InstagramPage from '../InstagramPage';
import YoutubePage from '../YoutubePage';
import TiktokPage from '../TiktokPage';
import FacebookPage from '../FacebookPage';
import LinkedinPage from '../LinkedinPage';
import XPage from '../XPage';
import InvalidUrl from '../InvalidUrl';
import { AuthContext } from '../../Context/auth';
import { useAccessVaildation } from '../../Context/accessValidation';

const ActionCenter = () => {
  const [currentTabUrl, setCurrentTabUrl] = useState('');
  const { authToken, setAuthToken } = useContext(AuthContext);
  const { hasAccess, checkAccess } = useAccessVaildation();
  const [isAllowedToRequest, toggleAllowToRequest] = useState(false);
  const initialTabId = useRef(null);

  useEffect(() => {
    const handleTabUpdated = (tabId, changeInfo, tab) => {
      if (
        changeInfo.status === 'complete' &&
        // tab.active &&
        tabId === initialTabId.current
      ) {
        setCurrentTabUrl(tab.url);
      }
    };

    // eslint-disable-next-line no-undef
    chrome.tabs.query({ active: true, currentWindow: true }, (tabs) => {
      initialTabId.current = tabs[0].id;
    });

    // eslint-disable-next-line no-undef
    chrome.tabs.onUpdated.addListener(handleTabUpdated);

    // eslint-disable-next-line no-undef
    chrome.cookies.get(
      { url: process.env.REACT_APP_PUBLIC_URL, name: 'AUTHTOKEN' },
      (cookie) => {
        setAuthToken(cookie ? cookie.value : '');
        checkAccess(cookie ? cookie.value : '');
      }
    );

    // eslint-disable-next-line no-undef
    return () => chrome.tabs.onUpdated.removeListener(handleTabUpdated);
  }, []);

  useEffect(() => {
    const changeUrlTrigger = (message, sender, sendResponse) => {
      if (message.action === 'updateUrl') {
        toggleAllowToRequest(true);
        // eslint-disable-next-line no-undef
        chrome.tabs.query({ active: true, currentWindow: true }, (tabs) => {
          initialTabId.current = tabs[0].id;
          setCurrentTabUrl(tabs[0].url);
        });
      }
    };
    // eslint-disable-next-line no-undef
    chrome.runtime.onMessage.addListener(changeUrlTrigger);
  }, []);

  const renderPage = () => {
    if (currentTabUrl.includes('instagram.com')) {
      return (
        <InstagramPage
          isAllowedToRequest={isAllowedToRequest}
          currentUrl={currentTabUrl}
          currCookie={authToken}
        />
      );
    } else if (currentTabUrl.includes('youtube.com')) {
      return (
        <YoutubePage
          isAllowedToRequest={isAllowedToRequest}
          currentUrl={currentTabUrl}
          currCookie={authToken}
        />
      );
    } else if (currentTabUrl.includes('tiktok.com')) {
      return (
        <TiktokPage
          isAllowedToRequest={isAllowedToRequest}
          currentUrl={currentTabUrl}
          currCookie={authToken}
        />
      );
    } else if (currentTabUrl.includes('facebook.com')) {
      return (
        <FacebookPage
          isAllowedToRequest={isAllowedToRequest}
          currentUrl={currentTabUrl}
          currCookie={authToken}
        />
      );
    } else if (currentTabUrl.includes('linkedin.com')) {
      return (
        <LinkedinPage
          isAllowedToRequest={isAllowedToRequest}
          currentUrl={currentTabUrl}
          currCookie={authToken}
        />
      );
    } else if (currentTabUrl.includes('x.com')) {
      return (
        <XPage
          isAllowedToRequest={isAllowedToRequest}
          currentUrl={currentTabUrl}
          currCookie={authToken}
        />
      );
    } else {
      return <InvalidUrl />;
    }
  };

  return (
    <div>
      <Routes>
        <Route index element={authToken && hasAccess ? renderPage() : <AccessGuard hasToken={authToken} hasAccess={hasAccess} />} />
        <Route
          path='/mezink-instagram'
          element={
            <InstagramPage currentUrl={currentTabUrl} currCookie={authToken} />
          }
        />
        <Route
          path='/mezink-youtube'
          element={
            <YoutubePage currentUrl={currentTabUrl} currCookie={authToken} />
          }
        />
        <Route
          path='/mezink-tiktok'
          element={
            <TiktokPage currentUrl={currentTabUrl} currCookie={authToken} />
          }
        />
      </Routes>
    </div>
  );
};

export default ActionCenter;
