import React, { useState, useContext } from "react";
import CloseIcon from "@mui/icons-material/Close";
import { LoadingContext } from "../../Context/loading";
import { ToasterContext } from "../../Context/toaster";

import styles from './AddToCampaign.module.css'
import apiService from "../../utils/service";

const AddToCampaign = (props) => {
  const {
    currCookie,
    username,
    type = "instagram",
    campaigns
  } = props;

  const [selectedCampaignId, setSelectedCampaignId] = useState(null);
  const [inputValue, setInputValue] = useState("");
  const [tags, setTags] = useState([]);
  const [email, setEmail] = useState("");
  const [phoneNumber, setPhoneNumber] = useState("");
  const [multiTags, setMultiTags] = useState('');

  const { setLoading } = useContext(LoadingContext);
  const { showToaster } = useContext(ToasterContext);

  const handleAddNewInfluencer = async () => {
    setLoading(true);
    let postBody = null;

    switch (type) {
      case "youtube":
        postBody = {
          youtubeUsername: username,
          tags: tags,
          email: email,
          phoneNumber: phoneNumber,
        };
        break;
      case "instagram":
        postBody = {
          instaUsername: username,
          tags: tags,
          email: email,
          phoneNumber: phoneNumber,
        };
        break;
      case "tiktok":
        postBody = {
          tiktokUsername: username,
          tags: tags,
          email: email,
          phoneNumber: phoneNumber,
        };
        break;
      case "x":
        postBody = {
          twitterUsername: username,
          tags: tags,
          email: email,
          phoneNumber: phoneNumber,
        };
        break;
      case "facebook":
        postBody = {
          facebookUsername: username,
          tags: tags,
          email: email,
          phoneNumber: phoneNumber,
        };
        break;
      case "LinkedIn":
        postBody = {
          linkedinUsername: username,
          tags: tags,
          email: email,
          phoneNumber: phoneNumber,
        };
        break;
      default:
        break;
    }

    try {
      const response = await apiService.post(
        `${process.env.REACT_APP_API_URL}/influencer/v1/manage`,
        postBody,
        {
          headers: {
            Authorization: `Bearer ${currCookie}`,
          },
        }
      );
      if (response.status === 200) {
        fetchInfluencerList();
        setTags([]);
        setEmail('');
        setPhoneNumber('');
      }
    } catch (error) {
      const errorMessage = error.response.data?.errors[0].detail || 'Failed to add Influencer, Please try again!'

      showToaster({
        severity: "error",
        show: true,
        message: errorMessage,
      });
      setLoading(false);
    }
  };

  const fetchInfluencerList = async () => {
    setLoading(true);
    const platformType = type === 'x' ? 'Twitter' : type
    try {
      const response = await apiService.get(
        `${process.env.REACT_APP_API_URL}/influencer/v1/list?minEngagementIG=-1&maxEngagementIG=-1&minEngagementTK=-1&maxEngagementTK=-1&minEngagementYT=-1&maxEngagementYT=-1&minEngagementFB=-1&maxEngagementFB=-1&minEngagementTW=-1&maxEngagementTW=-1&minEngagementLN=-1&maxEngagementLN=-1&minFollowersIG=-1&maxFollowersIG=-1&minFollowersTK=-1&maxFollowersTK=-1&minFollowersYT=-1&maxFollowersYT=-1&minFollowersFB=-1&maxFollowersFB=-1&minFollowersTW=-1&maxFollowersTW=-1&minFollowersLN=-1&maxFollowersLN=-1&search=${username}&type=${platformType}`,
        {
          headers: {
            Authorization: `Bearer ${currCookie}`,
          },
        }
      );
      if (response.status === 200 && response.data) {
        const influencerData = response.data;
        handleAddToCampaign(influencerData.data.influencers[0].id);
      }
    } catch (error) {
      const errorMessage = error.response.data?.errors[0].detail || 'Failed to get Influencer, Please try again!'

      showToaster({
        severity: "error",
        show: true,
        message: errorMessage,
      });
      setLoading(false);
    }
  };

  const handleAddToCampaign = async (theId) => {
    setLoading(true);

    const postBody = {
      campaignID: Number(selectedCampaignId),
      BrandInfluencerIDs: [Number(theId)],
    };

    try {
      await apiService.post(
        `${process.env.REACT_APP_API_URL}/campaign/v1/influencer/add`,
        postBody,
        {
          headers: {
            Authorization: `Bearer ${currCookie}`,
          },
        }
      );

      showToaster({
        severity: "success",
        show: true,
        message: "Influencer added to campaign",
      });
    } catch (error) {
      const errorMessage = error.response.data?.errors[0].detail || "Couldn't add influencer to campaign, please try again!"
      
      showToaster({
        severity: "error",
        show: true,
        message: errorMessage,
      });
    }
    setLoading(false);
  };

  const handleDropdownChange = (event) => {
    setSelectedCampaignId(event.target.value);
  };

  const handleInputChange = (event) => {
    setInputValue(event.target.value);
  };

  const handleInputKeyDown = (event) => {
    if (event.key === "Enter") {
      event.preventDefault();
      const newTag = inputValue.trim();
      if (newTag !== "" && !tags.includes(newTag)) {
        setTags([...tags, newTag]);
        setInputValue("");
      } else {
        setInputValue("");
      }
    }
  };

  const separateString = (string) => {
    return string.split(/\s*,\s*/).map(item => item.trim());
  }

  const separateTagsByComma = () => {
    const tagsCollection = separateString(multiTags)

    // filter out duplicate from new input
    const uniqueTagsCollection = tagsCollection.filter(tag => !tags.includes(tag))

    // filter out duplicate from state & from input
    const uniqueCombinedTags = [...new Set([...tags, ...uniqueTagsCollection])];

    setTags(uniqueCombinedTags)
  }

  const handleMultiTagsEnter = (e) => {
    if (e.key === 'Enter') {
      e.preventDefault()
      separateTagsByComma()
      setMultiTags('')
    }
  }

  const handleTagDelete = (theTag) => {
    setTags(tags.filter((tag) => tag !== theTag));
  };

  return (
    <div className={styles['add-campaign-card']}>
      <h3>Add Influencer to Campaign:</h3>
      {campaigns && campaigns.length ? (
        <div className={styles['add-campaign-form-fields']}>
          <select value={selectedCampaignId} onChange={handleDropdownChange}>
            <option value={null}>Select Campaign</option>
            {campaigns.map((item) => (
              <option key={item.id} value={item.id}>
                {item.slug}
              </option>
            ))}
          </select>
          <div className={styles['add-campaign-form-fields-item']}>
            <img src="/images/icon-tags.png" alt="Tags" />
            <input
              type="text"
              value={inputValue}
              onChange={handleInputChange}
              onKeyDown={handleInputKeyDown}
              placeholder="Add a tag and press Enter"
            />
          </div>
          <div className={styles['add-campaign-form-fields-item']}>
            <img src="/images/icon-tags.png" alt="Tags" />
            <input
              type="text"
              value={multiTags}
              onChange={(e) => (setMultiTags(e.target.value))}
              onKeyDown={handleMultiTagsEnter}
              placeholder="Separate tags by comma and press Enter"
            />
          </div>
          {tags && tags.length ? (
            <section className={styles['tags-section']}>
              {tags.map((tag, index) => (
                <span key={index} className={styles['single-tag']}>
                  <p>{tag}</p>
                  <CloseIcon
                    style={{ width: "14px", height: "14px", cursor: "pointer" }}
                    onClick={() => handleTagDelete(tag)}
                  />
                </span>
              ))}
            </section>
          ) : (
            ''
          )}
          <div className={styles['add-campaign-form-fields-item']}>
            <img src="/images/icon-email.png" alt="Email" />
            <input
              type="text"
              value={email}
              onChange={(event) => {
                setEmail(event.target.value);
              }}
              placeholder="Enter Email Address"
            />
          </div>
          <div className={styles['add-campaign-form-fields-item']}>
            <img src="/images/icon-phone.png" alt="Phone" />
            <input
              type="text"
              value={phoneNumber}
              onChange={(event) => {
                setPhoneNumber(event.target.value);
              }}
              placeholder="Enter Phone Number"
            />
          </div>
          <div className={styles['add-campaign-form-button-wrap']}>
            <button onClick={handleAddNewInfluencer}>+ Add Influencer</button>
          </div>
        </div>
      ) : (
        <small>No Campaigns Found</small>
      )}
    </div>
  );
};

export default AddToCampaign;
