import React, { useEffect } from 'react';
import Highcharts from 'highcharts';
import styles from './Charts.module.css'

const Chart = (props) => {
  const {
    containerId = 'chart-container',
    type = 'column',
    height = 'inherit',
    title,
    subtitle = null,
    showTitle = true,
    seriesData = [
      {
        name: 'Oil and gas extraction',
        data: [13.93, 10.63, 14.73, 8.67, 17.37],
      },
      {
        name: 'Oil ',
        data: [4.93, 7.63, 8.73, 11.67, 19.37],
      },
    ],
    yAxis = null,
    xAxis = null,
    plotOptions = {},
  } = props;

  const renderHighCharts = () => {
    Highcharts.chart(containerId, {
      chart: {
        type,
      },
      xAxis: xAxis,
      subtitle: subtitle,
      title: showTitle
        ? {
            text: (title && title.text) || '',
            align: (title && title.align) || 'left',
            y: title && title.y,
            style: title && title.style,
          }
        : { text: '' },
      yAxis,
      series: seriesData,
      plotOptions,
      credits: {
        enabled: false,
      },
    });
  };

  useEffect(() => {
    renderHighCharts();
  }, [seriesData]);

  return <div style={{ height }} className={styles['container']} id={containerId}></div>;
};

export default Chart;
