import React, { useEffect, useState } from "react";

import OptionsPage from "./components/OptionsPage";
import InvalidUrl from "../InvalidUrl";
import SuggestedUsersPage from "./components/SuggestedUsersPage";

const XPage = (props) => {
  const { currentUrl = "", currCookie = null, isAllowedToRequest } = props;

  const [username, setUsername] = useState(null);
  const [isUrlInvalid, toggleInvalidUrl] = useState(null);
  const [urlType, setUrlType] = useState(null);
  const [usernameId, setUsernameId] = useState(null);

  const checkXUrl = (url) => {
    // Define the regular expression patterns for valid username pages and connect_people URLs
    const usernamePattern = /^https:\/\/x\.com\/(?!home|explore|notifications|messages)([a-zA-Z0-9_]+)$/;
    const connectPeoplePattern = /^https:\/\/x\.com\/i\/connect_people\?user_id=(\d+)$/;
  
    // Test the URL against the patterns
    const usernameMatch = url.match(usernamePattern);
    const connectPeopleMatch = url.match(connectPeoplePattern);
  
    if (usernameMatch) {
      return {
        isValid: true,
        type: 'username',
        profileUsername: usernameMatch[1]
      };
    } else if (connectPeopleMatch) {
      return {
        isValid: true,
        type: 'connect_people',
        userId: connectPeopleMatch[1]
      };
    } else {
      return {
        isValid: false,
        profileUsername: null,
        userId: null
      };
    }
  }
  const renderViewByType = (viewType) => {
    if (viewType === 'username') {
      return (
        <OptionsPage
          currCookie={currCookie}
          username={username}
          isAllowedToRequest={isAllowedToRequest}
        />
      )
    } else if (viewType === 'connect_people') {
      return (
        <SuggestedUsersPage currCookie={currCookie} userId={usernameId} isAllowedToRequest={isAllowedToRequest} />
      )
    }
  }

  useEffect(() => {
    const { isValid, type, userId, profileUsername } = checkXUrl(currentUrl)

    if (isValid) {
      if (type === 'username') {
        toggleInvalidUrl(false)
        setUrlType(type)
        setUsername(profileUsername)
      } else if (type === 'connect_people') {
        toggleInvalidUrl(false)
        setUrlType(type)
        setUsernameId(userId)
      }
    } else {
      toggleInvalidUrl(true)
      setUsername(null)
    }
  }, [currentUrl])


  return (
    <>
      {isUrlInvalid ? (
        <InvalidUrl />
      ) : (
        <>
          {renderViewByType(urlType)}
        </>
      )}
    </>
  );
};

export default XPage;
