import styles from './TopPosts.module.css';

const TopPosts = (props) => {
  const { type, data, profilePic, totalCalculatedPosts } = props;

  const renderFetchMsg = (type, postsLength) => {
    switch (type) {
      case 'youtube-vid':
        return 'Data fetched for latest 20 videos';
      case 'youtube-shorts':
        return 'Data fetched for latest 20 shorts';
      case 'instagram':
        return 'Data fetched for latest 24 reels';
      case 'tiktok':
        return `Data fetched for latest ${postsLength} videos`;
      default:
        break;
    }
  };
  const compTitle = (type) => {
    switch (type) {
      case 'instagram':
        return 'Top 6 Reels';
      case 'tiktok':
        return 'Top 6 Videos';
      case 'youtube-vid':
        return 'Top 6 Videos';
      case 'youtube-shorts':
        return 'Top 6 Shorts';
      default:
        break;
    }
  };
  const formatDate = (dateString) => {
    const date = new Date(dateString);
    const options = {
      month: 'short',
      day: '2-digit',
      year: 'numeric',
      hour: '2-digit',
      minute: '2-digit',
      hour12: true
    };

    return date.toLocaleString('en-US', options);
  };
  const formatNumber = (num) => {
    if (num >= 1_000_000_000) {
      return (num / 1_000_000_000).toFixed(1).replace(/\.0$/, '') + 'B';
    } else if (num >= 1_000_000) {
      return (num / 1_000_000).toFixed(1).replace(/\.0$/, '') + 'M';
    } else if (num >= 1_000) {
      return (num / 1_000).toFixed(1).replace(/\.0$/, '') + 'K';
    } else {
      return num.toString();
    }
  };
  const formatER = (val) => {
    let formattedValue = val * 100;
    return `${formattedValue.toFixed(1)}%`;
  };
  const postClick = (url) => {
    window.open(url, '_blank');
  };

  return (
    <div className={styles['top-posts-box']}>
      <div className={styles['top-posts-header']}>
        <h3>{compTitle(type)}</h3>
        <p className={styles['small-msg']}>
          {renderFetchMsg(type, totalCalculatedPosts)}
        </p>
      </div>

      {data.topViewsPost && data.topViewsPost.length ? (
        <div className={styles['post-container']}>
          {data.topViewsPost.map((post, index) => (
            <div
              onClick={() => postClick(post.permalink)}
              className={styles['post-item']}
            >
              <div className={styles['post-item-header']}>
                <img
                  src={profilePic || '/images/no_image.png'}
                  alt='Profile Pic'
                />
                <p>{formatDate(post.date)}</p>
              </div>

              <div className={styles['post-item-img']}>
                <img
                  src={post.media_url || '/images/no_image.png'}
                  alt='Post'
                />
              </div>

              <p className={styles['post-item-desc']}>{post.caption}</p>

              <div className={styles['post-item-metrics']}>
                <div>
                  <img src='/images/icon-likes.png' alt='Like' />
                  <p>{formatNumber(post.likes)}</p>
                </div>
                <div>
                  <img src='/images/icon-comments.png' alt='Comments' />
                  <p>{formatNumber(post.comments)}</p>
                </div>
                <div>
                  <img src='/images/icon-engagement.png' alt='Engagement' />
                  <p>
                    {post.engagementRate ? formatER(post.engagementRate) : ''}
                  </p>
                </div>
              </div>
            </div>
          ))}
        </div>
      ) : (
        ''
      )}
    </div>
  );
};

export default TopPosts;
