export const formatValue = (num) => {
  if (num === null || num === undefined) {
    return 'N/A';
  } else if (num >= 1000000) {
    const millionValue = num / 1000000;
    return millionValue >= 100
      ? Math.round(millionValue) + 'M'
      : millionValue.toFixed(1) + 'M';
  } else if (num >= 1000) {
    const thousandValue = num / 1000;
    return thousandValue >= 100
      ? Math.round(thousandValue) + 'K'
      : thousandValue.toFixed(1) + 'K';
  } else {
    return num.toString();
  }
};
