import React, { useState, useEffect, useContext } from 'react';
import { CSVLink } from 'react-csv';

import styles from './OptionsPage.module.css';
import RefreshIcon from '@mui/icons-material/Refresh';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import SaveAltIcon from '@mui/icons-material/SaveAlt';
import MetricsSection from '../../../../components/MetricsSection';
import AddToCampaign from '../../../../components/AddToCampaign';
import InvalidUrl from '../../../InvalidUrl';
import { ToasterContext } from '../../../../Context/toaster';
import { LoadingContext } from '../../../../Context/loading';
import {
  formatISO,
  subDays,
  subHours,
  subMinutes,
  subMonths,
  subSeconds,
  subWeeks,
  subYears
} from 'date-fns';
import OpenInNewIcon from '@mui/icons-material/OpenInNew';
import apiService from '../../../../utils/service';
import AdvanceMetrics from '../../../../components/AdvanceMetrics';
import TopPosts from '../../../../components/TopPosts';
import AddPostToCampaign from '../../../../components/AddPostToCampaign';
import AddBulkInfluencer from '../../../../components/AddBulkInfluencer';
import CacheDataWarning from '../../../../components/CacheDataWarning';
import { useCacheableFetch } from '../../../../hooks/useCacheableFetch';

const OptionsPage = (props) => {
  const {
    currCookie,
    username,
    errorMsg,
    setErrorMsg,
    pageType,
    urlType,
    isAllowedToRequest
  } = props;

  const [status, setStatus] = useState('');
  const [metricsData, setMetricsData] = useState(null);
  const [postsCopy, setPostsCopy] = useState([]);
  const [activeTab, setActiveTab] = useState('metrics');
  const [advancedMetricsData, setAdvancedMetricsData] = useState({
    data: null,
    loading: false,
    error: false
  });
  const [suggestedUsers, setSuggestedUsers] = useState([]);
  const [posts, setPosts] = useState(null);
  const [campaigns, setCampaigns] = useState(null);
  const { showToaster } = useContext(ToasterContext);
  const { setLoading, loading } = useContext(LoadingContext);

  const handleFetchSuccess = ({
    metricsData: newMetricsData,
    advancedMetricsData: newAdvancedMetricsData
  }) => {
    setMetricsData(newMetricsData);
    setErrorMsg('');

    // Update advanced metrics state if it was fetched
    if (newAdvancedMetricsData) {
      setAdvancedMetricsData({
        data: newAdvancedMetricsData,
        loading: false,
        error: false
      });
    }
  };

  const { fetchWithCache, isCacheData } = useCacheableFetch({
    currCookie,
    onSuccess: handleFetchSuccess,
    useAdvancedMetrics: true
  });

  const postScrapedData = async (reqBody) => {
    setLoading(true);
    try {
      await fetchWithCache(reqBody);
    } catch (error) {
      const errorMessage =
        error.response.data?.errors[0].detail ||
        'Extension API error. Please try again.';

      if (error.code === 'ERR_CANCELED') {
        setErrorMsg('Aborting ongoing request. Please wait...');
      } else {
        setErrorMsg(errorMessage);
      }
    } finally {
      setLoading(false);
      setStatus('refresh');
    }
  };

  const postSuggestedInfluencer = async (
    theUsername,
    suggestedInfluencerUsername
  ) => {
    const postData = {
      username: theUsername,
      platform: 'linkedin',
      users: suggestedInfluencerUsername
    };

    try {
      const response = await apiService.post(
        `${process.env.REACT_APP_API_URL}/scraper/v1/suggested-influencer`,
        postData,
        {
          headers: {
            Authorization: `Bearer ${currCookie}`
          }
        }
      );
    } catch (error) {
      const errorMessage =
        error.response.data?.errors[0].detail ||
        'Failed to add suggested influencer data';

      showToaster({
        severity: 'error',
        show: true,
        message: errorMessage
      });
    }
  };

  const getLinkedinProfileData = () => {
    // eslint-disable-next-line no-undef
    chrome.tabs.query({ active: true, currentWindow: true }, (tabs) => {
      // eslint-disable-next-line no-undef
      chrome.tabs.sendMessage(
        tabs[0].id,
        { action: 'scrapeLinkedinProfile', username },
        (response) => {
          // console.log(response);
        }
      );
    });
    setLoading(false);
  };

  const getLinkedinRecentActivityData = (refresh = false) => {
    let profileFollower;
    // eslint-disable-next-line no-undef
    chrome.tabs.query({ active: true, currentWindow: true }, (tabs) => {
      // eslint-disable-next-line no-undef
      chrome.tabs.sendMessage(
        tabs[0].id,
        { action: 'scrapeLinkedinProfileActivity', isRefresh: refresh },
        (response) => {
          // console.log(response);
        }
      );
    });
    // eslint-disable-next-line no-undef
    chrome.storage.local.get('profileFollowers', (result) => {
      const profileExtraData = result.profileFollowers;

      if (profileExtraData.username === username) {
        profileFollower = profileExtraData.connectionFollowers;
      }
    });
    setTimeout(() => {
      // eslint-disable-next-line no-undef
      chrome.storage.local.get('linkedinProfileActivityData', (result) => {
        const getEstimatedDate = (dateStr) => {
          if (!dateStr) {
            return formatISO(new Date(), { representation: 'complete' });
          }

          const now = new Date();
          const value = parseInt(dateStr.match(/\d+/)?.[0] || '0', 10); // Get the numeric value
          const unit = dateStr.match(/[a-zA-Z]+/)?.[0]; // Get the unit part (e.g., s, m, h, d, w, mo, yr)
          let pastDate;

          switch (unit) {
            case 's':
              pastDate = subSeconds(now, value);
              break;
            case 'm':
              pastDate = subMinutes(now, value);
              break;
            case 'h':
              pastDate = subHours(now, value);
              break;
            case 'd':
              pastDate = subDays(now, value);
              break;
            case 'w':
              pastDate = subWeeks(now, value);
              break;
            case 'mo':
              pastDate = subMonths(now, value);
              break;
            case 'yr':
              pastDate = subYears(now, value);
              break;
            default:
              pastDate = new Date();
              break;
          }

          if (pastDate === '') {
            return formatISO(new Date(), { representation: 'complete' });
          } else {
            return formatISO(pastDate, { representation: 'complete' });
          }
        };
        const postDataToCopy = result.linkedinProfileActivityData.posts.map(
          (post) => ({
            url: post.url,
            like: post.reactions,
            reply: post.comments
          })
        );
        const activityData = result.linkedinProfileActivityData.posts.map(
          (item) => ({
            ...item,
            estimatedDate: getEstimatedDate(item.estimatedDate)
          })
        );
        const payload = {
          username: username,
          platform: 'linkedin',
          name: result.linkedinProfileActivityData.name,
          phone: '',
          email: '',
          followers: result.linkedinProfileActivityData.followers
            ? result.linkedinProfileActivityData.followers
            : profileFollower
            ? profileFollower
            : 0,
          avatar: result.linkedinProfileActivityData.profilePic,
          description: result.linkedinProfileActivityData.bio,
          posts: activityData.map((post) => ({
            url: post.url,
            thumbnailUrl: post.thumbnailUrl,
            caption: post.caption,
            shareCount: post.reposts,
            likeCount: post.reactions,
            date: post.estimatedDate,
            commentCount: post.comments,
            repost: post.isRepost
          }))
        };
        const suggestedUsersData =
          result.linkedinProfileActivityData.suggestedUsers.map((user) => ({
            avatar: user.profilePic,
            name: user.name,
            username: user.username,
            description: user.bio
          }));
        const postData = activityData.map((post) => ({
          thumbnail: post.thumbnailUrl,
          url: post.url
        }));

        setSuggestedUsers(suggestedUsersData);
        postSuggestedInfluencer(username, suggestedUsersData);
        postScrapedData(payload);
        setPostsCopy(postDataToCopy);
        setPosts(postData);
      });

      setLoading(false);
    }, 10000);
  };

  const extractLinkedinByUrlType = (refresh = false) => {
    setLoading(true);
    setMetricsData(null);
    setPostsCopy([]);

    if (urlType === 'profile') {
      getLinkedinProfileData();
    } else if (urlType === 'recent-activity') {
      getLinkedinRecentActivityData(refresh);
    } else if (urlType === 'company' && pageType) {
      setLoading(false);
      if (pageType === 'index') {
        // Handle main tab on company
      } else if (pageType === 'about') {
        // Handle about tab on company
      } else if (pageType === 'posts') {
        // Handle posts tab on company
      } else if (pageType === 'people') {
        // Handle people tab on company
      }
    }
  };

  const openRecentActivityPage = () => {
    // eslint-disable-next-line no-undef
    chrome.tabs.query({ active: true, currentWindow: true }, function (tabs) {
      const activeTab = tabs[0];
      if (activeTab && activeTab.id !== undefined) {
        // eslint-disable-next-line no-undef
        chrome.tabs.update(activeTab.id, {
          url: `https://www.linkedin.com/in/${username}/recent-activity/all/`
        });
      }
    });
  };

  const openSuggestedInfluDashboard = () => {
    window.open(
      `https://brand.mez.ink/suggested-influencer/${username}`,
      '_blank'
    );
  };

  const headers = [
    { label: 'Post URL', key: 'URL' },
    { label: 'Likes', key: 'Likes' },
    { label: 'Replies', key: 'Replies' }
  ];

  const formattedClipboardData = async () => {
    const headerRow = ['Post URL', 'Likes', 'Replies'];

    const dataRows = postsCopy.map((post) => [post.url, post.like, post.reply]);

    const allRows = [headerRow, ...dataRows];

    const formattedData = allRows.map((row) => row.join('\t')).join('\n');
    navigator.clipboard
      .writeText(formattedData)
      .then(() => {
        showToaster({
          severity: 'success',
          show: true,
          message: 'Data copied to clipboard'
        });
      })
      .catch((error) => {
        showToaster({
          severity: 'error',
          show: true,
          message: 'Failed to copy data to clipboard'
        });
      });
  };

  const formatSuggestedUsers = () => {
    const headerRow = ['Post URL', 'Likes', 'Replies'];

    const dataRows = postsCopy.map((post) => [post.url, post.like, post.reply]);

    const allRows = [headerRow, ...dataRows];

    const formattedData = allRows.map((row) => row.join('\t')).join('\n');

    const textarea = document.createElement('textarea');
    textarea.value = formattedData;
    document.body.appendChild(textarea);
    textarea.select();

    try {
      const successful = document.execCommand('copy');
      document.body.removeChild(textarea); // Clean up
      if (successful) {
        showToaster({
          severity: 'success',
          show: true,
          message: 'Data copied to clipboard'
        });
      } else {
        showToaster({
          severity: 'error',
          show: true,
          message: 'Failed to copy data to clipboard'
        });
      }
    } catch (error) {
      showToaster({
        severity: 'error',
        show: true,
        message: 'Failed to copy data to clipboard'
      });
    }
  };

  const fetchCampaigns = async () => {
    try {
      const response = await apiService.get(
        `${process.env.REACT_APP_API_URL}/campaign/v1/list`,
        {
          headers: {
            Authorization: `Bearer ${currCookie}`
          }
        }
      );

      setCampaigns(response.data.data);
    } catch (error) {
      const errorMessage =
        error.response.data?.errors[0].detail || 'Error fetching campaigns.';

      showToaster({
        severity: 'error',
        show: true,
        message: errorMessage
      });
    }
  };

  useEffect(() => {
    if (username && urlType && isAllowedToRequest) {
      if (loading) {
        apiService.cancelAllRequests();
        setLoading(false);
      }

      extractLinkedinByUrlType();
    }
  }, [username, urlType, isAllowedToRequest]);

  useEffect(() => {
    fetchCampaigns();
  }, []);

  return (
    <>
      {errorMsg ? (
        <InvalidUrl />
      ) : (
        <div className={styles['main-cont']}>
          <section className={styles['top-card']}>
            <div className={styles['left-box']}>
              <>
                <div className={styles['url-cx']}>
                  {username && (
                    <>
                      <h3>Username:</h3>
                      <p>{username}</p>
                    </>
                  )}
                </div>
              </>
            </div>
            <div className={styles['right-box']}>
              <div className={styles['button-box']}>
                {status === 'refresh' && (
                  <>
                    <div
                      className={styles['refresh-cta']}
                      onClick={() => getLinkedinRecentActivityData(true)}
                    >
                      <RefreshIcon
                        style={{ color: '#500b75', height: '20px' }}
                      />
                      <span>Refresh</span>
                    </div>
                  </>
                )}
                {urlType === 'profile' ? (
                  <div
                    className={styles['refresh-cta']}
                    onClick={openRecentActivityPage}
                  >
                    <OpenInNewIcon
                      style={{ color: '#500b75', height: '18px' }}
                    />
                    <span>Recent Activity Page</span>
                  </div>
                ) : (
                  ''
                )}
                <div
                  className={styles['refresh-cta']}
                  onClick={openSuggestedInfluDashboard}
                >
                  <OpenInNewIcon style={{ color: '#500b75', height: '18px' }} />
                  <span>Suggested Influencer Dashboard</span>
                </div>
                {urlType === 'recent-activity' ? (
                  <>
                    <button onClick={formattedClipboardData}>
                      <span>Copy Data to Clipboard</span>
                      <ContentCopyIcon
                        style={{ color: '#ffffff', height: '16px' }}
                      />
                    </button>
                    <CSVLink
                      data={postsCopy.map((item) => ({
                        URL: item.url,
                        Likes: item.like,
                        Replies: item.reply
                      }))}
                      headers={headers}
                      filename={`${username}_linkedin_data.csv`}
                    >
                      <span>Save Data to CSV</span>
                      <SaveAltIcon
                        style={{ color: '#500b75', height: '16px' }}
                      />
                    </CSVLink>
                    {suggestedUsers && suggestedUsers.length > 0 && (
                      <button onClick={formatSuggestedUsers}>
                        <span>Copy Suggested Users Data</span>
                        <ContentCopyIcon
                          style={{ color: '#ffffff', height: '16px' }}
                        />
                      </button>
                    )}
                  </>
                ) : (
                  ''
                )}
              </div>
            </div>
          </section>
          {urlType === 'recent-activity' ? (
            <>
              <section className={styles['tabs-section']}>
                <div className={styles['tabs-container']}>
                  <button
                    onClick={() => setActiveTab('metrics')}
                    style={
                      activeTab === 'metrics'
                        ? { color: '#500b75' }
                        : { color: '#000000' }
                    }
                    type='button'
                  >
                    Metrics
                  </button>
                  <button
                    onClick={() => setActiveTab('advanced-metrics')}
                    style={
                      activeTab === 'advanced-metrics'
                        ? { color: '#500b75' }
                        : { color: '#000000' }
                    }
                    type='button'
                  >
                    Advanced Metrics
                  </button>
                  <button
                    onClick={() => setActiveTab('top-posts')}
                    style={
                      activeTab === 'top-posts'
                        ? { color: '#500b75' }
                        : { color: '#000000' }
                    }
                    type='button'
                  >
                    Top 6 Posts
                  </button>
                  <button
                    onClick={() => setActiveTab('add-post')}
                    style={
                      activeTab === 'add-post'
                        ? { color: '#500b75' }
                        : { color: '#000000' }
                    }
                    type='button'
                  >
                    Add Post
                  </button>
                </div>
              </section>
              {isCacheData && (
                <section className={styles['bottom-card']}>
                  <CacheDataWarning />
                </section>
              )}
              {metricsData && activeTab === 'metrics' ? (
                <section className={styles['bottom-card']}>
                  <MetricsSection
                    data={metricsData.data}
                    type='linkedin'
                    postCount={postsCopy.length}
                  />
                </section>
              ) : (
                ''
              )}
              {activeTab === 'advanced-metrics' ? (
                advancedMetricsData.loading ? (
                  <section className={styles['advanced-metrics-section']}>
                    <p className={styles['section-placeholder']}>
                      Fetching data...
                    </p>
                  </section>
                ) : advancedMetricsData.error ? (
                  <section className={styles['advanced-metrics-section']}>
                    <p className={styles['section-placeholder']}>
                      No data available
                    </p>
                  </section>
                ) : advancedMetricsData.data ? (
                  <section className={styles['advanced-metrics-section']}>
                    <AdvanceMetrics
                      data={advancedMetricsData.data}
                      type='linkedin'
                      frequencyChartId='linkedin-frequency-chart'
                      erRateHistoryChartId='linkedin-er-history-chart'
                    />
                  </section>
                ) : (
                  ''
                )
              ) : (
                ''
              )}
              {activeTab === 'top-posts' ? (
                advancedMetricsData.loading ? (
                  <section className={styles['top-posts-section']}>
                    <p className={styles['section-placeholder']}>
                      Fetching data...
                    </p>
                  </section>
                ) : advancedMetricsData.error ? (
                  <section className={styles['top-posts-section']}>
                    <p className={styles['section-placeholder']}>
                      No data available
                    </p>
                  </section>
                ) : advancedMetricsData.data ? (
                  <section className={styles['top-posts-section']}>
                    <section className={styles['top-posts-section']}>
                      <TopPosts
                        data={advancedMetricsData.data}
                        profilePic={metricsData.data.avatar}
                        type='linkedin'
                      />
                    </section>
                  </section>
                ) : (
                  ''
                )
              ) : (
                ''
              )}
              {posts && posts.length && activeTab === 'add-post' ? (
                <section className={styles['top-posts-section']}>
                  <section className={styles['top-posts-section']}>
                    <AddPostToCampaign
                      campaigns={campaigns}
                      posts={posts}
                      currCookie={currCookie}
                      type='linkedin'
                      username={username}
                    />
                  </section>
                </section>
              ) : (
                ''
              )}
              {metricsData && (
                <section className={styles['middle-card']}>
                  <AddToCampaign
                    currCookie={currCookie}
                    metricsData={metricsData}
                    username={username}
                    suggestedUsers={suggestedUsers}
                    campaigns={campaigns}
                    type='LinkedIn'
                  />
                </section>
              )}
              {suggestedUsers && suggestedUsers.length > 0 && (
                <section className={styles['last-card']}>
                  <div>
                    <AddBulkInfluencer
                      suggestedUsers={suggestedUsers}
                      currCookie={currCookie}
                      type='linkedin'
                    />
                  </div>
                </section>
              )}
            </>
          ) : (
            ''
          )}
        </div>
      )}
    </>
  );
};

export default OptionsPage;
