import React, { useEffect, useState } from 'react';

import OptionsPage from './components/OptionsPage';
import InvalidUrl from '../InvalidUrl';

const FacebookPage = (props) => {
  const { currentUrl = '', currCookie = null, isAllowedToRequest } = props;

  const [userIdentifier, setUserIdentifier] = useState(null);
  const [errorMsg, setErrorMsg] = useState('');
  const [urlType, setUrlType] = useState('');
  const [isUrlInvalid, toggleInvalidUrl] = useState(null);

  const checkFacebookUrl = (url) => {
    // Define the regex patterns for groups, reel and page URLs
    const groupOrReelPattern =
      /^https:\/\/(www|web)\.facebook\.com\/(groups|reel)\/([\w-]+)\/?$/;
    const profileUserIdPattern =
      /^https:\/\/(www|web)\.facebook\.com\/profile\.php\?id=([\d]+)&sk=reels_tab(?:&[^#]*)?\/?$/;
    const profileUsernamePattern =
      /^https:\/\/(www|web)\.facebook\.com\/([\w.-]+)\/reels\/?(?:\?.*)?$/;
  
    // Test the URL against the group or reel pattern
    let match = url.match(groupOrReelPattern);
    if (match) {
      const urlType = match[1]; // Either 'groups' or 'reel'
      const path = match[2]; // The ID or path
  
      return {
        isValid: true,
        urlType: urlType,
        path: path
      };
    }
  
    // Test the URL against the profile pattern
    match = url.match(profileUserIdPattern);
    if (match) {
      const id = match[2]; // The ID from the query parameter
  
      return {
        isValid: true,
        urlType: 'reels-userid',
        path: id
      };
    }
  
    // Test the URL against the profile username pattern
    match = url.match(profileUsernamePattern);
    if (match) {
      const username = match[2]; // The username in the URL
  
      return {
        isValid: true,
        urlType: 'reels-username',
        path: username
      };
    }
  
    // If the pattern doesn't match, return invalid
    return {
      isValid: false,
      urlType: null,
      path: null
    };
  };

  useEffect(() => {
    const { isValid, urlType, path } = checkFacebookUrl(currentUrl);

    if (isValid && (urlType === 'reels-username' || urlType === 'reels-userid')) {
      setUrlType(urlType);
      setUserIdentifier(path);
    } else {
      toggleInvalidUrl(true)
      setUserIdentifier(null)
    }
  }, [currentUrl]);

  return (
    <>
      {isUrlInvalid ? (
        <InvalidUrl />
      ) : (
        <OptionsPage
          currCookie={currCookie}
          username={userIdentifier}
          errorMsg={errorMsg}
          setErrorMsg={setErrorMsg}
          urlType={urlType}
          isAllowedToRequest={isAllowedToRequest}
        />
      )}
    </>
  );
};

export default FacebookPage;
